.login-container {
  min-width: 280px;
  max-width: 460px;
  margin: 65px auto;
  .material-gradient(@dashblue, @white, 6px);
}

.fpCol {
  text-align: center;
  margin: 25px 0 10px;
}

.fpCol a.fpLink {
  font-size: 12px;
}

.login-content {
  padding: 25px 50px 15px;

  a.footerlink {
    color: @innovationblue;
    &:hover {
      text-decoration: underline;
    }
  }
}

.logologin {
  width: 100%;
}

.loginLogo {
  width: 300px;
  height: 90px;
  margin: 32px auto 40px;
}
@media (max-width: @screen-xs-max) {
  .login-content {
    padding: 25px 25px 15px 25px;
  }

  .loginLogo {
    width: 200px;
    height: 60px;
    margin: 32px auto 40px;
  }
}

.login-button {
  padding: 10px 15px;
  margin: 25px 0;
}
