.notes-2 {
  // correct issue with 2020 Kendo assigning explicit width to tree grids and not allowing expand to width within columns
  #show-pane-notes {
    table[role="treegrid"], 
    table[role="grid"] {
      width: 100%!important;
    }
}

  .add-note.k-button[disabled], .edit-note.k-button[disabled] {
    background-color: @text-disabled!important;
    &:hover {
      cursor: default!important
    }
  }

  #show-pane-notes {
    height: calc(100% - 61px);
  }
  
  .richtextparent {
    height: 100%;
  }

  #add-edit-notes {
    height: calc(100% - 23px);
    border:0;
  }

  td.note div {
    height: 18px;
    overflow: hidden;
  }

  td.k-state-focused {
    box-shadow: none;
  }

  #notes-detail {
    width: 300px;
  }

  #hide-pane-notes {
    p {
      width: 100%;
      text-align: center;
      margin-top: 2rem;
      .opacity(.54);
    }
  }

  .dropdown-menu {
    top: 0;
    margin-left: -150px;
    margin-top: 6px;
  }

  .actions {
    position: relative;
    overflow: visible;
  }

  .k-splitbar:not(.k-splitbar-draggable-vertical) {
    background-color: @white!important;
    border-color: @white!important;
  }

  .k-icon.k-resize-handle {
    background: none;
    border-bottom: 1px dashed #333;
  }

  table.k-editor {
      height: 100%;
  }

  table.k-editor iframe.k-content {
      height: calc(100% - 2px); 
  }

  .notes-list {
    overflow: hidden;

    .k-filter,
	  .k-i-filter {
      .q-small_Filter!important;
      width: 10px;
      height: 10px;
      position: relative;
      margin-top: -8px;
	  }

    .k-state-active {
      span.k-icon.k-i-filter {
        .opacity(1)!important;
        .q-smallc_Filter!important;
      }
    }

    .k-i-sort-desc-sm,
		.k-si-arrow-s {
			.q-content_arrow_down!important;
			width: 18px;
			height: 18px;
		}

		.k-i-sort-asc-sm,
		.k-si-arrow-n {
			.q-content_arrow_up!important;
			width: 18px;
			height: 18px;
		}

    .k-hierarchy-cell {
      padding:0;
      padding-left: 0.75rem;
    }

   .k-master-row {
     &.q-note-edit {
       background-color: @grid-row-selected;
       .note {
          &:before {
            content: "editing";
            font-size: 10px;
            font-weight: bold;
            text-transform: uppercase;
            position: absolute;
            padding: 4px;
            left: 0;
            margin-top: -18px;
            .opacity(0.54);
            }
         }
        }
        td {
          white-space: nowrap;
      }
    }

    .k-grid,
    .k-grid-header th,
    .k-grid-header tr,
    .k-grid-header {
      border: 0!important;
    }

    .expanded-row {
      border: 0;
      .note-area {
        margin-bottom: 1rem;
      }
      .note {
        white-space: normal;
      }
    }
  }

  

  .submit-wrapper {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    // -webkit-box-align: center;
    // -ms-flex-align: center;
    // align-items: center;
    // -webkit-box-orient: horizontal;
    // -webkit-box-direction: normal;
    // -ms-flex-flow: row nowrap;
    // flex-flow: row nowrap;
    // -webkit-box-pack: justify;
    // -ms-flex-pack: justify;
    // justify-content: space-between;

    .submit-childarea1,
    .submit-childarea2 {
      flex: 1;
      overflow-x: hidden;
      padding-right: 0.75rem;
      padding-left: 0.75rem;
    }

    .cancel-btn, .cancel-btn:hover {
      border: 0;
      background: transparent;
      background: transparent!important;
      width: 6rem;
      color: #333!important;
      box-shadow: none!important;
      padding: 0;
    }
  }



