#AttachContainer .k-grid tr td,
#NotesContainer .k-grid tr td {
  border-bottom-width: 0!important;
}

#NotesContainer {
  .k-state-focused {
    box-shadow: none;
    -webkit-box-shadow: none;
  }

  .k-state-selected {
    .k-icon {
      opacity: 0.45;

      &:hover {
        opacity: 0.75;
      }
    }
  }

  .k-pager-sizes {
    display: block;

    .k-widget.k-dropdown {
      margin-top: 2px;
      max-width: 60px;
    }
  }
}

.filtersearch {
  margin-right: 28px;
  display: inline-block;
  @media(max-width:767px) {
    width: 90%!important;
  }

  &:after {
    content: "";
    display: inline-block;
    .sprite;
    .q-content_View;
    position: absolute;
    top: 24px;
    right: -22px;
    .opacity(0.27);
  }
}

.autowidth {
  width: auto!important;
}

#Notes {
  .q-content_View {
    .q-content_Search();
  }
}

#DocAttach,
#GridRowDocAttach,
#GridRowNotes,
#Notes {

  span.k-icon.k-i-more-vertical {
    
    .q-content_overflow();
    padding: 0;
    height: 18px;
    width: 18px;
  }

  #toggleheight,
  #toggleheight2 {
    -webkit-transition: height 1s;
    -moz-transition: height 1s;
    -ms-transition: height 1s;
    -o-transition: height 1s;
    transition: height 1s;
    overflow: hidden;
    height: 356px;
    @media(max-width:768px) {
      overflow: auto;
      height: 440px;
    }
  }

  #toggleheight2 {
    height: 120px;
    overflow-x: hidden;
    overflow-y: auto;
  }

  h3 {
    margin-bottom: 16px!important;
    margin-left: 0;
    margin-top: 24px !important;
  }
  // Document Manager
  .k-edit {
    .q-content_Edit!important;
  }

  .k-delete {
    .q-content_Delete!important;
  }

  .k-state-selected {
    .actions,
    .detailscolumn {
      background: inherit;
    }
  }

  .actions {
    .k-delete,
    .k-edit {
      margin-left: 12px;
      @media(min-width:@screen-sm-max) {
        display: none;
      }
    }
  }

  .hidden {
    display: none;
  }

  .actions {
    .k-icon {
      width: 18px;
      height: 18px;
    }
  }

  #docgrid,
  #docgridrow {
    tbody:empty {
      &:after {
        content: "No attachments currently exist on this record";
        font-size: 13px;
        display: block;
        width: 100%;
        text-align: center;
        padding: 16px;
        color: @secondarytext;
      }
    }

    tr.k-state-selected,
    tr:hover {
      .k-delete,
      .k-edit {
        display: inline-block;
      }
    }
  }

  #notesgrid,
  #notesgridrow {
    tbody:empty {
      &:after {
        content: "No notes currently exist on this record";
        font-size: 13px;
        display: block;
        width: 100%;
        text-align: center;
        padding: 16px;
        color: @secondarytext;
      }
    }

    tr.k-state-selected,
    tr:hover {
      .k-delete,
      .k-edit {
        display: inline-block;
      }
    }
  }

  #docgrid tbody:empty {
    &:after {
      content: "No document attachments currently exist on this record"!important;
    }
  }

  .accordion {
    margin-right: 30px;
    margin-top: 4px;
  }
  // CSS added from QFC
  .docmgmt .k-tab-control-container {
    visibility: hidden;
  }

  .docatt-buttons {
    float: right;
    width: 40px;
    height: 15px;
    margin-top: 7px;
    margin-right: 5px;
    margin-left: 5px;
    background-color: #d3dce0;
  }

  .docmgmt-btns div:first-child {
    float: left;
    margin-right: 0.25em;
  }

  .doc-attachments-tab .k-grid-content tr > td:first-child,
  .doc-attachments-tab .k-grid-header-wrap tr > th:first-child {
    display: none;
  }

  .doc-attachments-tab .k-grid-content-locked tr > td:not(:nth-child(3)),
  .doc-attachments-tab .k-grid-header-locked tr > th:not(:nth-child(3)) {
    display: none;
  }

  .k-widget .k-dropzone {
    padding: 0;
  }

  .k-widget .k-upload-button {
    /*width: 40px;
		height: 40px;*/
    background-color: #5aabf7 !important;
  }

  .docmgmt-btns .k-upload-button {
    height: auto;
  }

  .notes-buttons {
    float: right;
    width: 40px;
    height: 15px;
    margin-top: 7px;
    margin-right: 5px;
    margin-left: 5px;
    background-color: #d3dce0;
  }

  .k-listview {
    min-width: 100%;
    min-height: 300px;
    padding: 0;
    border: 0;
  }

  .da-product-view dl:after,
  .k-listview:after {
    display: block;
    height: 0;
    clear: both;
  }

  .lv-product-view {
    position: relative;
    float: left;
    width: 99.8%;
  }

  .lv-edit-buttons {
    position: absolute;
    top: 0;
    right: 0;
    width: 100px;
    height: 100%;
  }

  .lv-edit-buttons .k-button {
    width: 100px;
    margin-bottom: 1px;
  }

  .lv-product-view dd {
    float: left;
    width: 450px;
    margin: 0;
  }

  .lv-product-view dl {
    float: left;
    min-width: 0;
    padding: 0;
    margin: 10px 0;
  }

  .lv-product-view dt {
    float: left;
    width: 200px;
    margin-right: 1em;
    clear: left;
    text-align: right;
    opacity: 0.6;
  }
  // Accordion text control /
  .accordiontext:after,
  .accordiontext:before {
    color: @innovationblue;
  }

  .accordiontext:hover:after,
  .accordiontext:hover:before {
    text-decoration: underline;
    color: @innovationblue;
  }

  .panel-body {
    padding: 0!important;
  }

  .notesPin.undock {
    i.q-content_pin {
      .q-content_unpin!important;
    }
  }
  // Notes grid
  .detailscolumn {
    width: calc(100% - 100px);
    line-height: 1.6em!important;
    display: block!important;
  }

  .actionscolumn {
    width: 100px;
  }

  .supportingdetails {
    display: block;
    min-width: 160px!important;
    margin: 10px 0 0;

    .spacer {
      margin-right: 32px;
      @media(max-width:@screen-xs-max) {
        display: block;
      }
    }
  }

  .details {
    margin: 10px 0;
  }

  .k-grid.k-widget {
    border-color: transparent!important;
  }

  .k-grid-content {
    overflow: auto!important;
  }

  .k-grid-header {
    display: none;
  }

  .k-grid tr {
    background: @white;
  }

  .k-grid tr.k-alt {
    background: @gray-lighter;
  }

  tr.k-state-selected {
    background: @grayhighlight!important;
  }
}

.zeroheight {
  margin: 0;
  padding: 0 32px;
  height: 0!important;
}

.k-mobile {
  #DocAttach,
  #GridRowDocAttach,
  #GridRowNotes,
  #Notes {
    .detailscolumn {
      width: 100%;
    }
  }

  #DocAttach,
  #Notes {
    .fullwindowmode {
      display: none;
    }
    h3 {
      padding-top: 16px;
    }
  }

  div#noteArea3 {
    position: absolute;
    margin-top: -40px;
    right: -20px;
  }

  .notesblock1 {
    margin-top: 12px;
  }
}
// Notes: fixed state
.fixednote {
  position: relative;

  .notesPin {
    float: right;
    margin-top: 24px;
  }

  .k-filename {
    max-width: calc(100% - 56px);
  }

  .docClose,
  .notesClose {
    float: right;
    margin-top: 24px;
    margin-left: 12px;
  }

  .smalltextarea {
    table.k-editor {
      height: 100px;

      td.k-editable-area {
        height: 60px!important;
      }
    }
  }

  table.k-editor {
    height: 100px;

    td.k-editable-area {
      height: 140px;
    }
  }
}

.disabledicon {
  .opacity(0.27)!important;
  cursor: default!important;

  &:hover {
    .opacity(0.27)!important;
  }
}
// Notes: floating state
.floatnote {
  background: @white;
  bottom: -10px;
  right: -2px;
  position: fixed;
  z-index: 1001;
  width: 800px;
  padding: 0 12px 8px;
  margin-bottom: 10px;
  .box-shadow(0 -2px 5px 1px rgba(0, 0, 0, 0.26));

    .k-overflow-tools {
      right: 44px;
    }

    .k-editor-toolbar-wrap {
      height: 64px;
      overflow: hidden;
      display: block;
    }


  .notesblock1,
  .notesblock2 {
    display: inline-block;
  }

  .notesblock1 {
    width: 240px;
    margin-left: 6px;
  }

  .notesblock2 {
    width: 540px;
    margin-left: 12px;
    height: 144px;
  }

  // .notesblock2 tr:nth-child(1) {
    // max-height: 50px;
    // position: relative;
    // display: block;
    // overflow: hidden;
  // }

  .notesPin {
    position: absolute;
    top: -126px;
    right: -500px;
    z-index: 100;
  }

  .docClose,
  .notesClose {
    position: absolute;
    top: -126px;
    right: -530px;
    z-index: 100;
  }

  table.k-editor {
    height: 30px;

    td.k-editable-area {
      height: 48px;
    }
  }
}

.k-editor.k-header {
  background: transparent;
}

table.k-editor {
  border: 0;
}

.k-overflow-container {
  .k-tool.k-button {
    background-color: transparent !important;
    box-shadow: none !important;
    &:hover {
      border: 0;
      background-color: @btn-default-border !important;
      box-shadow: none !important;
      border-radius: 0;
    }
  }
  .k-tool.k-button .k-tool-text {
    color: @primarytext;
    font-size: @font-size-base;
    font-weight: 300;
  }
}


.docClose,
.notesClose,
.notesPin {
  display: block;
  cursor: pointer;
  opacity: 0.54;

  &:hover {
    .opacity(1);
  }
}
@media(max-width:768px) {
  .notesPin {
    display: none;
  }
}
