.container,
.k-window,
.header-persona {
  @media only screen and (min-width: 761px) {
    // Scrollbar code for Webkit browsers

    ::-webkit-scrollbar {
      width: 15px;
      height: 15px;
    }
    // Track
    ::-webkit-scrollbar-track {
      background: @faintgray;
    }
    // Handle
    ::-webkit-scrollbar-thumb {
      -webkit-border-radius: 1px;
      border-radius: 1px;
      background: rgba(0,0,0,0.20);
    }

    ::-webkit-scrollbar-thumb:window-inactive {
      background: rgba(0,0,0,0.20);
    }
    // Old IE styles but IE11 seems to support
    scrollbar-base-color: #7f7f7f;
    scrollbar-base-color: #7f7f7f;
    scrollbar-3dlight-color: @white;
    scrollbar-highlight-color: @lighter-black;
    scrollbar-track-color: @lightgray;
    scrollbar-arrow-color: @gunmetal;
    scrollbar-shadow-color: @white;
    scrollbar-dark-shadow-color: @white;
  }
}
