//
// Alerts
// --------------------------------------------------

.k-button {
	background: transparent;
	border: 0;

	&:focus,
	&:hover {
		text-decoration: none;
		box-shadow: none;
		background: transparent;
	}
}

.k-window-actions{
	.k-button {
		background:transparent;
		border: 0;
		&:hover,
			&:focus {
				text-decoration: none !important;
				box-shadow: none !important;
				background:transparent !important;
			}
	}
}
.k-window-content {

	.delete-header {
		text-align: left;
		font-size: @font-size-large;
	}

	.delete-message {
		width: 100%;
		color: @secondarytext;
		text-align: left;
		font-size: @font-size-base;
	}
	.k-button, .delete-cancel, .delete-confirm {
		box-shadow: none;
		padding: 0.5rem;
		text-decoration: none;
		text-transform: uppercase;

		&:hover {
			text-decoration: none;
			border: 0!important;
			outline: none!important;
			text-decoration: none!important;
		}

		&:focus {
			text-decoration: none;
			box-shadow: none;
			background-color: @gray-lighter!important;
			border: 0!important;
			outline: none!important;
			text-decoration: none!important;
		}
	}

	.k-button:focus:not(.k-state-disabled):not([disabled]) {
		box-shadow: none;
	}
}

.k-notification {
	border: 0;
}

.alert-timer {
	color: @brand-danger;
}
// Info template
.k-notification-info.k-group {
	background: rgba(0%,0%,0%,.84);
	color: @white;
	display: block !important;
}
// Toast
// Commented out by Venkat's request
// Error template

.k-notification-error.k-group {
	color: @white;
}

.k-widget.k-notification.k-notification-error {
	background: transparent;
}
// Unused class
.wrong-pass {
	width: 300px;
	height: 100px;
}

.wrong-pass h3 {
	font-size: 1em;
	padding: 32px 10px 5px;
}

.wrong-pass img {
	float: left;
	margin: 30px 15px 30px 30px;
}

.alert-message {
	color: @primarytext;
	background: @white;
	.box-shadow(1px 1px 7px 4px rgba(128, 128, 128, 0.84));
	border-radius: 2px;
	max-width: 540px;
	min-width: 320px;
	max-height: 84%;
	max-height: 84vh!important;
	overflow-y: auto;
	@media(max-width: @screen-sm) {
		width: 280px;
	}

	.alert-header {
		display: block;
		color: @primarytext;
		padding: 0.5rem 2rem;

		h1 {
			font-size: @font-size-large;
		}

		span {
			color: @secondarytext;
		}
	}

	.alert-title,
	.alert-title-error,
	.alert-title-information,
	.alert-title-warning {
		border: 0;
		display: inline-block;
		width: 25%;
		min-width: 80px;
		padding-left: 2.5rem;
		vertical-align: top;
	}

	.alert-title-error {
		color: @brand-danger;
	}

	.alert-title-warning {
		color: @orange;
	}

	.alert-desc {
		display: inline-block;
		width: 60%;
		vertical-align: top;
		word-wrap: break-word;
		white-space: normal;
	}

	hr {
		display: block;
		width: 100%;
		color: @hinttext;
		background: @white;
	}

	a {
		color: @white;
		padding: 0.5rem 1.5rem;
	}
}

.alert-links {
	text-align: right;
	padding-bottom: 1.5rem;

}
/* parent of .k-notification-wrap */
.k-widget.k-notification.k-notification-success {
	background-color: transparent;
	border-color: transparent;
}

.k-notification-wrap {
	color: @white;
	min-width: 260px;
	max-width: 540px;
	white-space: normal;
	padding: 1.5rem 2.5rem;
	border-radius: 2px;
	background: rgba(0%,0%,0%,.7);
	margin-bottom: 2rem;
	.box-shadow(@string);

	.k-icon {
		display: none;
	}
	@media(max-width: @screen-sm) {
		width: 195px;
	}
}
// Data Alerts

.q-error,
.q-info,
.q-success,
.q-warning {
	font-weight: bold!important;
}

.q-error {
	color: @brand-danger!important;
}

.q-warning {
	color: @brand-warning!important;
}

.q-success {
	color: @brand-success!important;
}

.q-info {
	color: @brand-info!important;
}
