// List View
// Reset for strange behavior
.k-listview {
    border-color: transparent !important;
    .row {
        margin: 0 !important;
    }
    >*>div {
        font-size: 14px;
        color: @primarytext;
    }

    [class*="col-"] {
        padding-left: 0 !important;
        padding-right: 0 !important;
    }
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        margin: 0 0 16px 0 !important;
    } // List view styles
    .prelabel,
    .prelabel2 {
        display: block;
        height: 22px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        color: @hinttext;
    }
    .prelabel2 {
        display: none;
    }
    .initialrow {
        margin-bottom: 16px !important;
    }
    .rowholder {
        margin: 6px 0 48px 16px !important;
    }
    .rowcells {
        margin-left: 16px !important;
    }
    .rowcells>div {
        margin: 6px 0 !important;
    }
    .primaryaction span {
        margin-top: 40px;
    }
    .iconlink span {
        .opacity(0.54);
    }
    .iconlink:hover span {
        .opacity(0.87);
    }
    .icontext {
        cursor: pointer;
        &:hover {
            cursor: pointer;
        }
    }
    .rowcells:hover {
        background-color: @gray-faded;
    }
    @media (max-width: @screen-xs-max) {
        .prelabel2 {
            display: block;
        }
        .rowcells {
            margin: 6px 0 !important;
        }
        .primaryaction span {
            margin-top: 0;
        }
    }
}