// Title styling for text-overflow:ellipsis controls

.alertlabel[data-title],
.datalabel[data-title],
.errorfreelabel[data-title],
.q-header_Item[data-title],
.unitselect[data-title],
.widget h1[data-title],
.widget h2[data-title],
.widget h3[data-title],
.widget h4[data-title],
.widget h5[data-title],
.widget h6[data-title],
.widgetfooter a[data-title] {
  &:hover:after {
    content: attr(data-title)!important;
    padding: 6px;
    position: absolute;
    left: 0;
    top: 70%;
    white-space: nowrap;
    z-index: 10001!important;
    color: @primarytext;
    font-size: 12px;
    line-height: 14px;
    background: @tooltip-bg;
    .box-shadow(@string);
    .border-top-radius(4px);
    .border-bottom-radius(4px);
  }
}

.widget h1[data-title],
.widget h2[data-title],
.widget h3[data-title],
.widget h4[data-title],
.widget h5[data-title],
.widget h6[data-title] {
  &:hover:after {
    top: 10%;
    margin-left: 16px;
    opacity: 0;
  }
}

.alertlabel[data-title],
.datalabel[data-title],
.errorfreelabel[data-title] {
  &:hover:after {
    top: 1%;
    margin-left: 6px;
    opacity: 0;
  }
}
