.ind-tst, .ind-dev, .ind-dmo {
    #page {
        // background: -moz-linear-gradient(top, rgba(249,249,249,1) 0%, rgba(249,249,249,1) 70px, rgba(249,249,249,1) 70px, rgba(249,249,249,0) 70px); /* FF3.6-15 */
        // background: -webkit-linear-gradient(top, rgba(249,249,249,1) 0%,rgba(249,249,249,1) 70px,rgba(249,249,249,1) 70px,rgba(249,249,249,0) 70px); /* Chrome10-25,Safari5.1-6 */
        // background: linear-gradient(to bottom, rgba(249,249,249,1) 0%,rgba(249,249,249,1) 70px,rgba(249,249,249,1) 70px,rgba(249,249,249,0) 70px); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        background: transparent;
    }

    .q-actionbar:before {
        width: 48px;
        height: 16px;
        display:block;
        text-align: center;
        transform: rotate(-90deg);
        -webkit-transform: rotate(-90deg);
        -moz-transform: rotate(-90deg);
        -ms-transform: rotate(-90deg);
        -o-transform: rotate(-90deg);
        position: absolute;
        left: -16px;
        top: calc(100vh - 98px);
        letter-spacing: 2px;
        font-size: 1.125rem;
        z-index: 1;
    }
    .login-container {
        .fpCol {
            .label.label-default {
            color: @white;
            padding: 4px;
            border-radius: 4px;
            }
        }
    }
}

.ind-tst {
    background: -moz-linear-gradient(left, #67bb00 0%, #67bb00 4px, #eaf1f4 4px); /* FF3.6-15 */
    background: -webkit-linear-gradient(left, #67bb00 0%,#67bb00 4px,#eaf1f4 4px); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right, #67bb00 0%,#67bb00 4px,#eaf1f4 4px); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */

    // .context-selector {
    //     background: -moz-linear-gradient(left, #67bb00 0%, #67bb00 4px, #e0e0e0 4px); /* FF3.6-15 */
    //     background: -webkit-linear-gradient(left, #67bb00 0%,#67bb00 4px,#e0e0e0 4px); /* Chrome10-25,Safari5.1-6 */
    //     background: linear-gradient(to right, #67bb00 0%,#67bb00 4px,#e0e0e0 4px); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    // }
    .header-new h1  {
        color: @ind-tst;
        .header-text {
            color: @ind-tst;
        }
    }  
    .q-actionbar:before {
        content: "TST";
        background: @ind-tst;
    }
    .login-container {
      .material-gradient(@ind-tst, @white, 6px);
        .fpCol {
            .label.label-default {
            background-color: @ind-tst;
                &:before {
                    content: "TST: ";
                }
            }
        }
    }
  } 
  
