//
// Custom Quorum box-shadow
// --------------------------------------------------

.material-gradient (@startColor: #eee, @endColor: white, @percentgrad: 0.5%) {
 	background-color: @startColor;
	background-image: -webkit-gradient(linear, left top, right top, from(@startColor), to(@endColor));
	background-image: -webkit-linear-gradient(left, @startColor @percentgrad, @endColor @percentgrad);
	background-image: -moz-linear-gradient(left, @startColor @percentgrad, @endColor @percentgrad);
	background-image: -ms-linear-gradient(left, @startColor @percentgrad, @endColor @percentgrad);
	background-image: -o-linear-gradient(left, @startColor @percentgrad, @endColor @percentgrad);
	background-repeat: no-repeat;
}
