
.loadingindicator {
	background-image: url(images/loadingindicator.gif);
	background-size: 26px 26px;
	background-position: center center;
	background-repeat: no-repeat;
}

.loadingindicatorsmall {
	background-image: url(images/loadingindicator.gif);
	background-size: 13px 13px;
	background-position: center center;
	background-repeat: no-repeat;
}

span.k-icon.k-loading {
	.loadingindicatorsmall!important;
}

.k-autocomplete span.k-icon.k-loading {
    margin-right: 1px;
}

div.loadimage:before {
	content: " "!important;
	display:block;
	width:30px;
	height:30px;
	margin: 0 auto;

	.loadingindicator!important;

}

#lockText {
	margin-top:24px;
	color:@primarytext;
}

.LockOff {
    display: none;
}

.LockOnInvisible {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10009;
    display: block;
    width: 100%;
    height: 100%;
    padding-top: 20%;
    text-align: center;
    background-color: transparent;
    opacity: 0;
    filter: alpha(opacity=75);
    display:none;
}

.LockOn {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10009;
    display: block;
    width: 100%;
    height: 100%;
    padding-top: 20%;
    text-align: center;
    background-color: @white;
    opacity: 0.9;
    filter: alpha(opacity=75);
    display:block;
}

div.loadimage:before {
   content: url('../images/spinner.gif');
}
