// New action drop down classes over grid
.backgriddrop {
	position: absolute;
	min-width: 212px;
	width: 212px;
	z-index: 99999;
	border: solid 1px @context-bar;
	background: @white;
	padding: 0;
	margin: 0;
	font-size: 14px;
	-moz-box-shadow: 1px 1px 7px 4px rgba(128, 128, 128, 0.84) !important;
	-webkit-box-shadow: 1px 1px 7px 4px rgba(128, 128, 128, 0.84) !important;
	box-shadow: 1px 1px 7px 4px rgba(128, 128, 128, 0.84) !important;
}

.griddrop {
	display: block!important;
	position: relative!important;
	margin-top: -10px;
	margin-bottom: -10px;
	margin-left: -8px;

	li {
		text-align: left;
		width: 100%;

		a {
			display: block;
			// min-height: 18px!important;
			line-height: 12px; /* changed from 44px */
			padding-top: 6px;
			span {
				margin-top: 4px;
			}
		}

		a,
		button {
			font-size: 12px;
			width: 200px;
			min-height: 24px;
			white-space: normal;
			text-align: left;
			border: 0;
			background: transparent;
			padding: 2px 6px 3px;

			&:hover {
				background: @lightgray;
			}

			span:first-child {
				display: inline-block!important;
				margin-right: 12px!important;
				vertical-align: top!important;
			}

			span:last-child {
				display: inline-block!important;
				width: calc(100% - 40px)!important;
				color: @black;
			}
		}
	}
}

.droptool {
	&:hover {
		cursor: pointer!important;
	}
}

.k-mobile .gridresponsive {
	tr td.actionarea .droptool,
