// Any style classes for the Accordion pattern

.accordion.collapsed {
	i {
		.q-content_expand;
	}
}

.accordion {
	i {
		.q-content_collapse;
	}
}

.accordion.collapsed,
.accordion:not(.collapse) {
	margin-top: -14px;
	height: 14px;
}

.accordion ~ div {
	h3 {
		margin-top: 4px;
	}
}

.accordion {
	width: calc(100% - 28px);
	text-align: right;
	z-index: 1;
}

.accordion.smallactivearea {
	width: 30px!important;
}
