// Accordion Grids
.contentaccordion {
	padding: 16px 32px;
}

.empty {
	.opacity(0.54);

	.infodata {
		height: 20px;
	}
}

.nodata {
	.opacity(0.54);
}

.nodata:after {
	content: "No Data";
	display: inline-block;
	font-size: 13px;
	margin-left: 12px;
	.opacity(0.54);
}

.q-accordionview {
	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		margin: 0 0 24px 0!important;
	}

	.smalliconholder {
		display: inline-block;
		padding-left: 12px;
	}

	.tooltip {
		position: absolute !important;
	}

	.singlerow {
		padding: 16px 0 16px 32px;
		width: calc(100% - 80px);
		margin-left: 32px;
	}
	// List view styles
	.prelabel,
	.prelabel2 {
		display: block;
		height: 22px;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
		color: @hinttext;
	}

	.prelabel2 {
		display: none;
	}

	.initialrow {
		margin-bottom: 16px;
	}

	.primaryaction span {
		margin: 16px 10px 0 0;
	}

	.secondaction span {
		margin: 4px 10px 0 0;
	}

	.iconlink span {
		.opacity(0.54);
	}

	.iconlink:hover span {
		.opacity(0.87);
		cursor: pointer;
	}

}

@media(max-width: @screen-sm) {
	.accordion {
		position: relative !important;
		display: block !important;
		margin: -12px 0 0 0 !important;
		padding: 0 !important;
		height: 46px !important;
		width: 100% !important;
		text-align: center;
		right: unset !important;
	}
}