.handsonresize {
  height: 92%!important; // Height as a percentage stops the vertical scroll bar showing - doesnt seem to be an issue when bulk edit is forced into full screen

  .wtHider,
  .wtSpreader {
    height: 100%!important;
  }
}

#bulkEdit {
  overflow: hidden;
}

#bulkEdit.k-window-content {
  padding: 0 1.333em;
}

.lbbuttonholder {
  margin-top: 8px;
  margin-bottom: 16px;
}

.handsontable {
  font-size: 12px;

  th {
    background-color: @white!important;
  }

  tr:nth-child(odd) td:not(.highlight),
  tr:nth-child(odd) th:not(.highlight) {
    background: darken(@slightgray, 4%)!important;
  }

  &.ht_clone_top tr th:nth-child(n+1),
  &.ht_clone_top_left_corner tr th:nth-child(n+1) {
    background: @white!important;
  }

  td.area {
    background-color: #EEF4FF!important;
  }

  .ht_clone_top {
    .wtSpreader {
      border-bottom: 1px solid @faintgray;
      background: @white;
    }
  }

  .ht_clone_left {
    border-right: 0!important;
  }

  .handsontableInput {
    width: inherit!important;
  }

  tr:first-child td,
  tr:first-child th {
    border-top: 0!important;
  }

  th:first-child {
    border-left: 0!important;
  }
}

.ht_clone_left {
  background: @white;
  /*border-right: 1px solid @grayborder;*/
}

.handsontable .handsontable table {
  .box-shadow(none)!important;
}
// .handsontable thead th .relative {
// 	/*padding:6px 4px;*/
// }

.handsontable {
  th {
    background-color: @white!important;
    color: @secondarytext;
    border-color: @faintgray!important;
    text-align: left!important;

    .currentRow {
      text-align: left;
    }
  }

  thead th {
    padding: 4px!important;
  }

  td {
    border-color: @faintgray!important;
  }
}

.actioncontrols,
.handsoncontrols {
  .refresh {
    float: right;
    margin-right: 0;
    .opacity(0.27);
  }

  .intro {
    margin-right: 32px;
  }

  a {
    .opacity(0.54);
    text-indent: -9999px;
    margin-right: 32px;

    &:hover {
      cursor: pointer;
      .opacity(0.87);
    }
  }
}

.actioncontrols {
  border: 0!important;
}
