
.context-selector {
	// background: @context-bar;
	min-height: 48px;
	// padding: 6px 0;
	width: 100%;
	// box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
	&:hover {
		background: #dee1e3;
	}
  }

  .context-holder {
	display: flex;
	height: 100%;
  }

  .context-selector-content {
	flex: 1;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	overflow-x: auto;
  }

  .context-selector-edit {
	flex: 0;
	width: 90px;
	min-height: 48px;
  }

  .horizontal-holder {
	display: flex;
	flex-direction: column;
	justify-content: center;
	height: 100%;
  }

  .edit-area {
	height: 24px;
  }

  .context-selector-content-block {
	display: flex;
	height: 24px;
	margin: 0 24px 4px 0;
	flex: 1;
  }

  .context-name,
  .context-label,
  .context-value {
	line-height: 24px;
	white-space: nowrap;
  }

  .context-name {
	font-weight: bold;
	padding-right: 4px;
	width: 64px;
  }

  .context-label {
	min-width: 100px;
	padding-right: 4px;

  }

  .context-value {
	min-width: 100px;
	padding-right: 4px;
  }

  .context-notset {
	color: @brand-danger;
  }

  .context-value span {
	display: inline-block;
	height: 24px;
	padding: 0 12px;
	border-radius: 16px;
	border: 1px solid rgba(0, 0, 0, 0.27);
  }
