// Nested tab backgrounds and arrow background color
.k-tabstrip-wrapper {
	position: relative;
	display: block;
	margin: 0 auto;
	padding: 0 2px;
	border-radius: 4px;
	background: transparent; // Old browsers
	@media(max-width:@screen-md-max) {
		background-image: -webkit-linear-gradient(left, rgba(255,255,255,0) 0, rgba(237,237,237,1) 0);
	}
	@media(min-width:@screen-lg-min) {
		background-image: -webkit-linear-gradient(left, rgba(255,255,255,0) 0, rgba(237,237,237,1) 0);
	}
	background-image: -moz-linear-gradient(left, rgba(255,255,255,0) 0, rgba(237,237,237,1) 0);
	background-image: -o-linear-gradient(left, rgba(255,255,255,0) 0, rgba(237,237,237,1) 0);
	background-image: -ms-linear-gradient(left, rgba(255,255,255,0) 0, rgba(237,237,237,1) 0);
	background-repeat: no-repeat;
	background-size: 100% 46px;

	.k-tabstrip-wrapper {
		background: none;
	}
}

.k-tabstrip:focus {
	box-shadow: none;
}

