// Set rows for correct display in tabs
.k-tabstrip .k-content {
		margin-top: @font-size-base;
}

.k-tabstrip .k-content,
.k-tabstrip .q-tab-panel {
	.row {
		width: 100%;
	}
}

.q-tab-panel.extraheight {
  min-height: 300px;
}

// Tabs in a lightbox
.k-window {
	.k-tabstrip-wrapper {
		background: none;
	}
}
// No Data tab
