// }

.nodatatab {
	.nodatalink,
	a,
	span.k-link {
		.opacity(0.54);
	}
}
// Error tab
.errortab,
.infotab,
.warningtab {
	padding-left: 10px!important;

	&:before {
		content: "";
		display: inline-block;
		.opacity(0.84);
		position: absolute;
		top: 13px;
		left: 4px;
		width: 18px;
		height: 18px;
		.sprite!important;
	}
}

.errortab.k-state-active,
.warningtab.k-state-active {
	&:before {
		.opacity(1);
	}
}

.errortab:before {
	.q-contentc_Error!important;
}

.warningtab:before {
	.q-contentc_Warning!important;
}

.infotab:before {
	.q-contentc_Info!important;
}

