// Any style classes for the custom Wizard - short and long pattern

.stepwizard-step label.step-label {
  padding-left: 4px;
  padding-right: 4px;
  background-color: #F4F2F2;
  color: #B0AAAA;
  font-size: 14px;
  display: none;
  @media(min-width:992px) {
    display: inline-block!important;
  }
}

.stepwizard-step label.number-label {
  padding-left: 2px;
  padding-right: 2px;
  background-color: #F4F2F2;
  color: #B0AAAA;
  font-size: 14px;
  display: none;
  @media(max-width:991px) {
    display: inline-block!important;
  }
}

.stepwizard-step a,
.stepwizard-step label {
  top: 2px;
  position: relative;
}

.stepwizard-row {
  display: table-row;
  height: 100%;
  width: 100%;
}

.vertical .stepwizard-row {
  display: block;
  height: 100%;
  width: 100%;
}

.stepwizard {
  display: table;
  width: 99.7%;
  position: relative;
  border-bottom: 1px solid @grayborder;
  color: #B0AAAA;
  margin-left: 6px;
  padding: 0;
  background-color: @lightergray;
  height: 55px;
  @media(max-width:991px) and (min-width:768px) {
    width: 99.4%;
    margin-left: 6px;
  }
  @media(max-width:768px) {
    width: 99.4%;
    margin-left: 5px;
  }
}

.step-info {
  margin: 0 0 0 4px!important;
  background-color: transparent;
  width: 99.7%;
  padding: 0;
  @media(max-width:991px) and (min-width:768px) {
    width: 99.2%;
    margin-left: 6px!important;
  }
  @media(max-width:768px) {
    width: 98%;
    margin-left: 8px!important;
  }
}

.step-info h2 {
  padding-left: 20px;
  padding-top: 15px;
  height: 60px;
  font-size: 15px;
  background-color: @gray-faded;
  margin: 0 0 0 4px !important;
}

.step-info h3 {
  margin: 64px 0 16px 0!important;
  .opacity(0.54);
}

.step-info h3:first-child {
  margin-top: 16px!important;
}

.stepwizard-step button[disabled] {
  opacity: 1 !important;
}

.stepwizard-row:before {
  bottom: 0;
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: @grayborder;
  z-index: 0;
}

.stepwizard-step {
  display: table-cell;
  text-align: center;
  position: relative;
  height: 100%;
  padding: 10px 0;
}

.vertical .stepwizard-step {
  display: inline-block;
  text-align: center;
  position: relative;
  height: 100%;
  width: 10%;
  @media(max-width:767px) and (min-width:565px) {
    width: 10%;
  }
  @media(max-width:565px) and (min-width:475px) {
    width: 15%;
  }
  @media(max-width:475px) {
    width: 15%;
  }
  padding: 10px 0;
}
@media(min-width:992px) and (max-width:1280px) {
  .stepwizard-step.step-first {
    width: 12%;
  }
}

.q-form-wizard {
  margin: 0 0 10px;
}

.q-form-wizard .form-group {
  margin-top: 20px;
  margin-bottom: 20px;
}
@media(min-width:992px) {
  .q-form-wizard .form-group.left {
    margin-right: 40px;
    margin-left: 80px;
  }

  .q-form-wizard .form-group.right {
    margin-right: 80px;
    margin-left: 40px;
  }
}
@media(max-width:991px) {
  .q-form-wizard .form-group.left,
  .q-form-wizard .form-group.right {
    margin-right: 0px!important;
    margin-left: 0px!important;
  }

  .step-info h2 {
    margin: 0!important;
  }
}

.q-form-wizard .step-info {
  padding-left: 20px;
  @media(max-width:768) {
    padding-left: 5px;
  }
}

.q-form-wizard .form-group .form-control {
  display: block;
  width: 100%;
  height: auto;
}

.q-form-wizard .form-group .control-label {
  display: inline-block;
  max-width: 100%;
  margin-bottom: 5px;
  font-weight: 700;
}

.q-form-wizard .btn-link.pull-right {
  border-color: transparent;
  color: @dashblue;
  background-color: transparent;
  font-size: 14px;
  text-transform: uppercase;
  padding-right: 20px;
}

.q-form-wizard .btn-link.pull-right.tabwizard {
  padding-right: 10px;
  padding-left: 40px;
  @media(max-width:991px) {
    padding-left: 12px!important;
  }
}

.q-form-wizard .btn-link.pull-left.tabwizard {
  padding-left: 15px;
  @media(max-width:991px) {
    padding-left: 12px!important;
  }
}

.q-form-wizard .btn-link.pull-left {
  border-color: transparent;
  color: #888385;
  background-color: transparent;
  margin-top: 20px!important;
  font-size: 14px;
  text-transform: uppercase;
  padding-left: 20px;
}

.stepwizard-step .btn-circle,
.tabwizard-default .btn-circle {
  width: 30px;
  height: 30px;
  text-align: center;
  font-size: 12px;
  line-height: 1.428571429;
  border-radius: 15px;
}

.stepwizard-step .btn-circle {
  padding: 7.5px 0;
}

.tabwizard-default .btn-circle {
  padding: 6px 0;
}

.stepwizard-step .btn-primary,
.tabwizard-default .btn-primary {
  display: inline-block;
  color: @white;
  background-color: @dashblue;
  border-color: @dashblue;
  background-image: none;
}

.stepwizard-step .btn-default,
.tabwizard-default .btn-default {
  display: inline-block;
  color: @mediumgray-lighter;
  background-color: #B0AAAA;
  border-color: @mediumgray-lighter;
  background-image: none;
}

.stepwizard-step .btn-default[disabled] {
  background-color: #B0AAAA!important;
}

.stepwizard-step.step-first {
  text-align: left!important;
  padding-left: 20px!important;
  @media(max-width:564px) {
    padding-left: 0px!important;
  }
}

.stepwizard-step.step-second {
  text-align: left!important;
  right: 0;
  @media(max-width:991px) {
    right: 0;
  }
  @media(min-width:992px) and (max-width:1280px) {
    right: 0;
  }
}

.stepwizard-step.step-middle {
  display: none;
}

.stepwizard-step.step-middle-visible {
  text-align: left!important;
  right: 0;
  @media(max-width:991px) {
    right: 0;
  }
  @media(min-width:992px) and (max-width:1280px) {
    right: 0;
  }
}

.stepwizard-step.step-third {
  text-align: left!important;
  right: 0;
  @media(max-width:991px) {
    right: 10px;
  }
  @media(min-width:992px) and (max-width:1280px) {
    right: 0;
  }
}

.stepwizard-step.step-last {
  text-align: left!important;
  right: 0;
  @media(max-width:991px) {
    right: 10px;
  }
  @media(min-width:992px) and (max-width:1280px) {
    right: 0;
  }
}

.vertical .stepwizard-step.step-last {
  text-align: left!important;
  right: 0px!important;
}

.stepwizard-step.step-first.verticle {
  text-align: left!important;
  width: 1%;
}

.stepwizard-step.step-middle.verticle {
  text-align: center!important;
  width: 1%;
}

.stepwizard-step.step-last.verticle {
  text-align: right!important;
  width: 1%;
}

.content.k-content.wizard {
  width: 100%;
  padding: 0;
}

.content.k-content.wizard .row {
  width: 100%;
  padding: 0;
  margin: 0;
}

.content.k-content.wizard.vertical {
  border-top: 1px solid @grayborder;
}

.wizard .tabwizard {
  padding-left: 0;
}

.tabwizard-default {
  background-color: @gray-faded;
  border-bottom: 1px solid @grayborder;
  height: 60px;
  font-size: 14px;
}

.tabwizard-default.last {
  border-bottom: none;
}

.nav li.tabwizard-default .inline-anchor-left a {
  margin: 15px 0 15px 20px;
  font-size: 14px;
  position: absolute;
}

.nav li.tabwizard-default .inline-anchor-right a {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  padding: 20px 0 15px 24px;
  font-size: 14px;
}

.tabwizard-default .inline-anchor-left {
  width: 12%;
  height: 100%;
}

.tabwizard-default .inline-anchor-right {
  width: 85%;
  height: 100%;
}

.tabwizard-default .inline-anchor-left,
.tabwizard-default .inline-anchor-right {
  display: inline-block;
  position: relative;
}

.nav li.tabwizard-default {
  cursor: pointer;
}

.nav li.tabwizard-default:hover {
  background-color: darken(@gray-faded, 15%);
}

.wizard .onlysmall {
  display: none;
  @media(max-width:991px) {
    display: block;
    background-color: #F4F2F2;
    border: 1px solid @grayborder;
  }
}

.wizard .row .nosmall {
  display: block;
  padding: 0;
  @media(max-width:991px) {
    display: none;
  }
}

.wizard .row .wizardstyle {
  padding: 0;
  z-index: 8;
}

.wizard .row .wizard-header.wizardstyle {
  width: auto;
  margin-left: 0;
  height: 60px;
  padding: 0 0 0 20px;
}

.wizard .step {
  width: 99.2%;
  margin-left: 0;
}

.wizard .wizardstyle.step {
  border-bottom: 1px solid @grayborder;
  text-align: left;
  color: @grayborder;
  padding-bottom: 20px;
  padding-left: 20px;
}

.wizard-header.wizardstyle.step h1 {
  font-size: 18px;
  padding-top: 10px;
}
@media(min-width:992px) {
  .q-tabwizard .q-form-wizard .step-info h2 {
    display: none;
    color: rgb(128,128,128);
  }
}

.stepwizard-step.hline hr {
  display: block;
