/* Info wizard */
.wizard-infobox,
.wizard-infodetails {
  display: block;
  width: calc(100% - 3px);
  min-height: 50px;
  margin-left: 0;

  h1,
  h2,
  h3,
  h4,
  h5,
  p {
    margin: 0;
    padding: 0;
  }

  h1,
  h2,
  h3,
  h4,
  h5 {
    font-size: 15px;
    padding: 20px 0 15px 20px;
    width: 100%;

    .infotrigger {
      float: right;
      .opacity(0.54);
      margin-right: 16px;

      &:hover {
        .opacity(0.86);
      }
    }
  }
}

.material {
  .wizard-infobox {
    background: @gray-lighter;

    h3 {
      margin-top: 0!important;
      margin-bottom: 0!important;
    }
  }
}

.wizard-infodetails {
  background: @gray;
  padding: 12px 0;

  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p,
  span {
    color: @white;
    padding: 0 0 0 20px;
  }
}

.infolabel {
  .q-form-control-label;
  .opacity(1);
  display: block;
  margin-top: 6px;
}

.infodata {
  display: block;
  margin-bottom: 6px;
}

.content.k-content.wizard .row {
  min-width: 320px;
}
/* Error state */
.wizarderror.tab-pending,
.wizardinfo .tab-pending,
.wizardwarning .tab-pending {
  .opacity(1)!important;
}

.wizarderror,
.wizardinfo,
.wizardwarning {
  .errorcount {
    background: none!important;
    font-size: @font-size-base;
    right: 0;
    position: absolute;
    top: 0;
    line-height: 20px;
  }
}

.wizarderror {
  .q-wizard_complete,
  .q-wizard_current,
  .q-wizard_next {
    .q-wizard_error!important;
  }

  .btn-default {
    background-color: @brand-danger!important;
  }

  .errorcount {
    color: @brand-danger;
  }
}

.wizardwarning {
  .q-wizard_complete,
  .q-wizard_current,
  .q-wizard_next {
    .q-wizard_warning!important;
  }

  .btn-default {
    background-color: @brand-warning!important;
  }

  .errorcount {
    color: @brand-warning;
  }
}

.wizardinfo {
  .q-wizard_complete,
  .q-wizard_current,
  .q-wizard_next {
    .q-wizard_info!important;
  }

  .btn-default {
    background-color: @brand-info!important;
  }

  .errorcount {
    color: @brand-info;
  }
}

.nextBtn,
.prevBtn {
  margin-top: 6px!important;
  padding: 10px!important;
  display: inline-block;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  overflow: hidden;
  -webkit-transition-property: color;
  transition-property: color;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;

  &:before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: @off-white;
    border-radius: 100%;
    -webkit-transform: scale(0);
    transform: scale(0);
    -webkit-transition-property: transform;
    transition-property: transform;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
  }

  &:active:before,
  &:hover:before {
    -webkit-transform: scale(2);
    transform: scale(2);
  }

  &:focus:before {
    background: @faintgray;
  }
}
