#notice-template {
    display: none;
}

.notices-wrapper--loading {
    bottom: -120px !important;
    transition: all 1s linear;
}

.notices-wrapper {
    position: fixed;
    bottom: 0;
    height: 90px;
    width: 100%;
    overflow: visible;
    z-index: 999;
    transition: height 0.5s ease;
    transition-delay: 0s;
    &.notice-simple {
        .notices-left-col {
            display: none;
        }
        .notices-right-col {
            border-left: none;
            width: calc(100% - 40px);
        }
    }
}

.notices-wrapper--expanded {
    height: 40vh;
    transition: height 0.22s ease;
}

.notice-inner-wrapper {
    margin: 0;
}

.notice-overlay {
    width: 100%;
    height: 60px;
    background: rgba(0, 91, 153, 0);
    background: -moz-linear-gradient(top, rgba(0, 91, 153, 0) 0%, rgba(0, 91, 153, 0.09) 9%, rgba(0, 91, 153, 0.37) 37%, rgba(0, 91, 153, 0.61) 61%, rgba(0, 91, 153, 1) 100%);
    background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(0, 91, 153, 0)), color-stop(9%, rgba(0, 91, 153, 0.09)), color-stop(37%, rgba(0, 91, 153, 0.37)), color-stop(61%, rgba(0, 91, 153, 0.61)), color-stop(100%, rgba(0, 91, 153, 1)));
    background: -webkit-linear-gradient(top, rgba(0, 91, 153, 0) 0%, rgba(0, 91, 153, 0.09) 9%, rgba(0, 91, 153, 0.37) 37%, rgba(0, 91, 153, 0.61) 61%, rgba(0, 91, 153, 1) 100%);
    background: -o-linear-gradient(top, rgba(0, 91, 153, 0) 0%, rgba(0, 91, 153, 0.09) 9%, rgba(0, 91, 153, 0.37) 37%, rgba(0, 91, 153, 0.61) 61%, rgba(0, 91, 153, 1) 100%);
    background: -ms-linear-gradient(top, rgba(0, 91, 153, 0) 0%, rgba(0, 91, 153, 0.09) 9%, rgba(0, 91, 153, 0.37) 37%, rgba(0, 91, 153, 0.61) 61%, rgba(0, 91, 153, 1) 100%);
    background: linear-gradient(to bottom, rgba(0, 91, 153, 0) 0%, rgba(0, 91, 153, 0.09) 9%, rgba(0, 91, 153, 0.37) 37%, rgba(0, 91, 153, 0.61) 61%, rgba(0, 91, 153, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#005b99', endColorstr='#005b99', GradientType=0);
    z-index: 1000;
    position: absolute;
    bottom: 0;
    transition: height 0.22s cubic-bezier(0.19, 1, 0.22, 1);
    transition-delay: 0s;
}

.notices-wrapper--expanded {
    .notice-overlay {
        height: 0;
        transition-delay: 0.2s;
        transition: height 0.44s cubic-bezier(0.47, 0, 0.745, 0.715);
    }
}

.notice {
    background-color: @dashblueDark;
    border-top: 25px solid @dashblueDark;
    position: absolute;
    width: 100%;
    box-shadow: 0 -1px 4px 0 rgba(0, 0, 0, 0.26);
    transition: all 0.22s linear;
}

.notices-wrapper--expanded .notice {
    top: 0;
    left: 0;
    transition-delay: 0;
    transition: all 0.05s linear;
    box-shadow: 0 -2px 5px 0 rgba(0, 0, 0, 0.26);
    overflow: auto;
}

.notice p,
.notice h2,
.notice li,
.notice #message * {
    margin-top: 0;
    color: #fff;
}

.notices-left-col {
    li {
        list-style-type: none;
    }

    ul {
        padding: 0;
    }
}

.notice h2 {
    font-size: 14px;
    margin-bottom: 10px !important;
}

@media (max-width: 990px) {
    .notices-left-col h2#category-title {
        width: calc(100% - 40px);
    }
}

.notices-wrapper:not(.notice-simple) .notices-right-col h2#message-header {
    width: calc(100% - 40px);
}

.notice .k-button {
    border: 1px solid #fff;
    background: @dashblueDark  !important;
    padding: 9px 35px;
    margin: 0 0 9px 0;
    box-shadow: none;
}

.notice .k-button:hover {
    box-shadow: none !important;
}

.notice .k-button::before {
    content: none;
}

.notice-placeholder:nth-of-type(3),
.notice-placeholder:nth-of-type(4) {
    top: -20px;
    left: 20px;
    transition: all 0.22s linear;
}

.notice--latest {
    margin-top: 90px;
    transition: all 0.22s linear;
}

.notice--active {
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 999;
    margin-top: 0;
    transition: all 0.22s ease;
}

.notice--active.notice--hovering {
    top: -5px !important;
    transition: all 0.15s ease;
}

.notice--removing {
    top: 130px;
    left: 0px;
    bottom: -20px;
    opacity: 100;
    z-index: 1000;
    transition: all 2s ease-out;
}

.notice--removing+.notice-placeholder {
    top: -10px;
    left: 10px;
    z-index: 2;
    transition: all 0.22s ease;
}

.notice--active+.notice-placeholder {
    top: -10px;
    left: 10px;
    z-index: 2;
    transition: all 0.22s ease;
}

.notices-wrapper--expanded {
    .notice-placeholder {
        top: 0;
        z-index: 2;
        transition: all 0.22s ease;
    }
}

.notices-wrapper--expanded .notice-placeholder {
    box-shadow: none;
}

.notices-expand-icon {
    transform: rotate(180deg);
    transition-delay: 0.44s;
}

.notices-wrapper--expanded .notices-expand-icon {
    transform: rotate(0deg);
    transition-delay: 0.44s;
}

.notice-controls {
    position: absolute;
    z-index: 1000;
}

.notice-controls .notices-close {
    margin-left: 10px;
}

.notice-controls i:hover {
    cursor: pointer;
}

@media (min-width: 991px) {
    .notices-right-col {
        border-left: 1px solid #fff;
    }
}

.notices-right-col #eff-date {
    font-style: italic;
    font-weight: 100;
}

.notices-close:hover {
            background-color: #004a7d !important;
    }