actionbar,
header,
alert,
footer,
nav
{
    display: block;
    z-index: 1; /* To prevent inactive drop downs interferring - likely temp - DONT include a z-index for <section> */
    background: @dashblue;   
}

aside {
    z-index: 2; /* To prevent inactive drop downs interferring - likely temp */
}
/* Main CSS Grid App Layout on body */

#page {
    margin: 0;
    padding: 0;
    overflow: auto;
    width: 100vw;
    height: 100vh;
    display: -ms-grid;
    /* At worst we can put this in a div or applayout if other items like lightboxes cause issues when rendered in the body - better to keep it shallow if we can */
    -ms-grid-columns: auto 1fr auto;
    /* Auto always expands to the height of the content */
    -ms-grid-rows: auto auto auto 1fr auto;
    /* Auto always expands to the height of the content */
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-template-rows: auto auto auto 1fr auto;
    background-color: #eaf1f4;
    overflow: hidden;
}

header {
    background: transparent;
    -ms-grid-column: 1;
    -ms-grid-row: 1;
    -ms-grid-column-span: 3;
    grid-column: 1 / 4;
    /* Could be written as grid-column: 1 / span 3 - represents grid-column-start / grid-column-end which are also individual attributes */
    grid-row: 1;
    /* Could be written as grid-row: 1 / span 1 or grid-row: 1 (no end is needed)  */
    height: 40px;
}

actionbar {
    background: transparent;
    -ms-grid-column: 2;
    -ms-grid-row: 2;
    -ms-grid-column-span: 1;
    grid-column: 2;
    grid-row: 2;
    /* Shares same space as context */
}

alert {
    background: transparent;
    -ms-grid-column: 2;
    -ms-grid-row: 3;
    -ms-grid-column-span: 1;
    grid-column: 2;
    grid-row: 3;
    overflow-y: hidden;
}

nav {
    background: transparent;
    -ms-grid-column: 1;
    -ms-grid-row: 2;
    -ms-grid-row-span: 3;
    -ms-grid-column-span: 1;
    grid-column: 1;
    grid-row: 2 / 5;
    z-index: 10;
}

section {
    background: transparent;
    -ms-grid-column: 2;
    -ms-grid-row: 4;
    -ms-grid-column-span: 1;
    grid-column: 2;
    grid-row: 4;
    overflow-y: auto;
    overflow-x: auto; /* Overflow-x: hidden lead to bug 138490  */
    padding: 0 16px;
}

aside {
    background: transparent;
    -ms-grid-column: 3;
    -ms-grid-row: 2;
    -ms-grid-column-span: 1;
    -ms-grid-row-span: 3;
    grid-column: 3;
    grid-row: 2 / 5;
    overflow-y: hidden !important;
    overflow-x: auto;
    display: flex; /* allow easy stacking of docked panes */
    flex-wrap: nowrap;  /* Ensure the panes overflow and don't wrap */
    max-width: 800px; 
}

footer {
    background: transparent;
    -ms-grid-column: 1;
    -ms-grid-row: 5;
    -ms-grid-column-span: 3;
    grid-column: 1 / 4;
    grid-row: 5;
}

/* Hierarchy for drop downs */
.header-new {
    z-index: 3;
}
footer {
    z-index: 3;
}
actionbar {
    z-index: 2;
}
/* Sub layouts */
.sectionLayout {
    height: calc(100% - 8px);
}