// Doc manager

.freefloat {
  position: absolute;
  right: 24px;
  top: -12px;
}

.fixednote {
  .k-upload-files {
    line-height: @line-height-small;
  }

  .k-upload-status {
    top: 6px;
    right: 62px;
  }

  ul.k-upload-files.k-reset {
    overflow-y: scroll;
    height: 400px;
  }

  li.k-file .file-wrapper {
    margin-top: 12px;

    .k-upload-action {
      position: absolute;
      top: 0;
      right: 0;
    }
  }

  .file-heading {
    display: inline-block;
  }

  li.k-file div.file-wrapper {
    position: relative;
    height: 35px;
  }

  .filetype {
    display: inline-block;
    margin-right: 12px;
    .opacity(0.54);
  }
}

.prominentlink {
  color: @dashblue;

  &:hover {
    text-decoration: underline;
  }
}

.attachFile {
  position: relative;
  overflow: hidden;
  margin: 10px;
}

.attachFile input.attach {
  position: absolute;
  top: 0;
  right: 0;
  margin: 0;
  padding: 0;
  cursor: pointer;
  .opacity(0);
}

.filedisplay {
  width: auto;
  border: 0;
  margin-top: 14px;
  width: calc(100% - 150px);
  background: transparent;
}

.q-hiddenupload {
  display: inline-block;
  width: 18px;
  min-width: 18px!important;

  .k-upload-button {
    .opacity(0.54);
    width: 18px;
    height: 18px;

    &:hover {
      .opacity(0.87);
    }
  }

  .k-widget {
    display: none;
  }
}
