
.error-container {
  min-width: 280px;
  max-width: 460px;
  margin: 65px auto;
  .material-gradient(@dashblue, @white, 6px);

  h1,
  p {
    text-align: center;
    width: 100%;
  }

  p {
    margin-top: 20px;
  }
}

.error-content {
  padding: 25px 25px 15px;
}

.logoerror {
  width: 100%;
}

.erroranim {
  width: 150px;
  height: 150px;
  margin: 30px auto 60px;
  .opacity(0.54);
}

.errorLogo {
  width: 143px;
  height: 27px;
  margin: 60px 0 10px auto;
}
@media (max-width: @screen-xs-max) {
  .error-content {
    padding: 25px 25px 15px 25px;
  }

  .errorLogo {
    width: 200px;
    height: 45px;
    margin: 42px auto 40px;
  }
}
