// Any style classes for the Filter Dataset pattern
// Loading screen
@keyframes loadingText {
  0% {
    transform: scale(1);
  }

  25% {
    transform: scale(1.04);
  }

  75% {
    transform: scale(0.96);
  }

  100% {
    transform: scale(1);
  }
}
// Add class .lock-test to QFC helper files

#lockText {
	animation-name: loadingText;
  animation-duration: 1.6s;
	animation-iteration-count: infinite;
	animation-timing-function: linear;
}
// Accordion text links
@keyframes accordionlink {
  0% {
    top: 0;
  }

  25% {
    top: 2px;
  }

  50% {
    top: -2px;
  }

  100% {
    top: 0;
  }
}

@keyframes accordionlink {
	0%   {top:0px;}
    25%  {top:2px;}
    50%  {top:-2px;}
    100% {top:0px;}
	}


.accordiontext:hover {
  &:after {
    .opacity(0.54);
    animation-name: accordionlink;
    animation-duration: 0.5s;
    animation-timing-function: ease-in-out;
  }
}
// Tooltips
@keyframes tooltipanimation {
  from {
    opacity: 0.1;
    transform: scale(.3);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

.alertlabel[data-title],
.datalabel[data-title],
.errorfreelabel[data-title],
.q-header_Item[data-title],
.unitselect[data-title],
.widget h1[data-title],
.widget h2[data-title],
.widget h3[data-title],
.widget h4[data-title],
.widget h5[data-title],
.widget h6[data-title],
.widgetfooter a[data-title] {
  &:hover:after {
    .opacity(0)!important;
    animation-name: tooltipanimation;
    animation-duration: 0.5s;
    animation-delay: 0.25s;
    animation-fill-mode: forwards;
  }
}

.tooltip {
  .opacity(0)!important;
  animation-name: tooltipanimation;
  animation-duration: 0.5s;
  animation-delay: 0.25s;
  animation-fill-mode: forwards;
}
// Widgets
// Fade opacity of footerlinks on hover
@keyframes footerlinkfade {
  0% {
    .opacity(0.54);
  }

  75% {
    .opacity(1);
  }

  100% {
    .opacity(0.87);
  }
}
@-webkit-keyframes footerlinkfade {
  0% {
    .opacity(0.54);
  }

  75% {
    .opacity(1);
  }

  100% {
    .opacity(0.87);
  }
}
@-moz-keyframes footerlinkfade {
  0% {
    .opacity(0.54);
  }

  75% {
    .opacity(1);
  }

  100% {
    .opacity(0.87);
  }
}
@-o-keyframes footerlinkfade {
  0% {
    .opacity(0.54);
  }

  75% {
    .opacity(1);
  }

  100% {
    .opacity(0.87);
  }
}

.footer-add,
.footer-closed,
.footer-open,
.footer-pending,
.footer-recallreput,
.footer-submit,
.footer-tool,
.footer-view,
.footer-withdraw {
  &:hover {
    .opacity(0.87);
    animation: footerlinkfade 0.5s ease-in-out;
    animation: footerlinkfade 0.5s ease-in-out;
    -webkit-animation: footerlinkfade 0.5s ease-in-out;
    -moz-animation: footerlinkfade 0.5s ease-in-out;
    -ms-animation: footerlinkfade 0.5s ease-in-out;
    -o-animation: footerlinkfade 0.5s ease-in-out;

    &:before {
      .opacity(0.87);
      animation: footerlinkfade 0.5s ease-in-out;
      -webkit-animation: footerlinkfade 0.5s ease-in-out;
      -moz-animation: footerlinkfade 0.5s ease-in-out;
      -ms-animation: footerlinkfade 0.5s ease-in-out;
      -o-animation: footerlinkfade 0.5s ease-in-out;
    }
  }
}

.footer-animate {
    &:hover {
      .opacity(0.87);
      animation: footerlinkfade 0.5s ease-in-out;
      animation: footerlinkfade 0.5s ease-in-out;
      -webkit-animation: footerlinkfade 0.5s ease-in-out;
      -moz-animation: footerlinkfade 0.5s ease-in-out;
      -ms-animation: footerlinkfade 0.5s ease-in-out;
      -o-animation: footerlinkfade 0.5s ease-in-out;

      &:before {
        .opacity(0.87);
        animation: footerlinkfade 0.5s ease-in-out;
        -webkit-animation: footerlinkfade 0.5s ease-in-out;
        -moz-animation: footerlinkfade 0.5s ease-in-out;
        -ms-animation: footerlinkfade 0.5s ease-in-out;
        -o-animation: footerlinkfade 0.5s ease-in-out;
      }
    }
  }

// Show clickable area
@keyframes viewappear {
  0% {
    .opacity(0);
    margin: 0 10px 10px 0;
  }

  100% {
    .opacity(0.54);
    margin: 0 15px 10px 0;
  }
}
@-webkit-keyframes viewappear {
  0% {
    .opacity(0);
    margin: 0 10px 10px 0;
  }

  100% {
    .opacity(0.54);
    margin: 0 15px 10px 0;
  }
}
@-moz-keyframes viewappear {
  0% {
    .opacity(0);
    margin: 0 10px 10px 0;
  }

  100% {
    .opacity(0.54);
    margin: 0 15px 10px 0;
  }
}
@-o-keyframes viewappear {
  0% {
    .opacity(0);
    margin: 0 10px 10px 0;
  }

  100% {
    .opacity(0.54);
    margin: 0 15px 10px 0;
  }
}

.hovdownload,
.hovshow {
  &:before {
    margin: 0 15px 10px 0;
    content: " ";
    display: block;
    right: 0;
    bottom: 0;
    position: absolute;
    .opacity(0);
  }

  &:hover {
    &:before {
      .opacity(0.54);
      animation: viewappear 0.5s ease-out;
      -webkit-animation: viewappear 0.5s ease-out;
      -moz-animation: viewappear 0.5s ease-out;
      -ms-animation: viewappear 0.5s ease-out;
      -o-animation: viewappear 0.5s ease-out;
      margin: 0 15px 10px 0;
    }
  }
}
// Show data interaction on mouse over
@keyframes datainteract {
  0% {
    .opacity(0.87);
  }

  50% {
    .opacity(0.54);
  }

  100% {
    .opacity(0.87);
  }
}
@-webkit-keyframes datainteract {
  0% {
    .opacity(0.87);
  }

  50% {
    .opacity(0.54);
  }

  100% {
    .opacity(0.87);
  }
}
@-moz-keyframes datainteract {
  0% {
    .opacity(0.87);
  }

  50% {
    .opacity(0.54);
  }

  100% {
    .opacity(0.87);
  }
}
@-o-keyframes datainteract {
  0% {
    .opacity(0.87);
  }

  50% {
    .opacity(0.54);
  }

  100% {
    .opacity(0.87);
  }
}

.hovdownload:hover,
.hovshow:hover {
  .alertdata,
  .alertdata:before,
  .alertdatasmall,
  .alertdatasmall:before,
  .data,
  .data:before,
  .datasmall,
  .datasmall:before,
  .download span {
    .opacity(0.87);
    animation: datainteract 0.5s ease-in-out;
    -webkit-animation: datainteract 0.5s ease-in-out;
    -moz-animation: datainteract 0.5s ease-in-out;
    -ms-animation: datainteract 0.5s ease-in-out;
    -o-animation: datainteract 0.5s ease-in-out;
  }
}
// Click interactions on widgets

.arealink, .q-widget-interaction-clickable {
  overflow: hidden;
}

.colorpop {
  display: block;
  position: absolute;
  background: rgba(0, 0, 0, 0.15);
  border-radius: 100%;
  -webkit-transform: scale(0);
  -moz-transform: scale(0);
  -o-transform: scale(0);
  transform: scale(0);
}

.animate {
  -webkit-animation: pop 0.65s linear;
  -moz-animation: pop 0.65s linear;
  -ms-animation: pop 0.65s linear;
  -o-animation: pop 0.65s linear;
  animation: pop 0.65s linear;
}
@-webkit-keyframes pop {
  100% {
    opacity: 0;
    -webkit-transform: scale(2.5);
  }
}
@-moz-keyframes pop {
  100% {
    opacity: 0;
    -moz-transform: scale(2.5);
  }
}
@-o-keyframes pop {
  100% {
    opacity: 0;
    -o-transform: scale(2.5);
  }
}
@keyframes pop {
  100% {
    opacity: 0;
    transform: scale(2.5);
  }
}
// Widget Shadow Hover
@keyframes widgethover {
  0% {
    .box-shadow(@string);
  }

  100% {
    .box-shadow(0 5px 8px 0 rgba(0, 0, 0, 0.36));
  }
}
@-webkit-keyframes widgethover {
  0% {
    .box-shadow(@string);
  }

  100% {
    .box-shadow(0 5px 8px 0 rgba(0, 0, 0, 0.36));
  }
}
@-moz-keyframes widgethover {
  0% {
    .box-shadow(@string);
  }

  100% {
    .box-shadow(0 5px 8px 0 rgba(0, 0, 0, 0.36));
  }
}
@-o-keyframes widgethover {
  0% {
    .box-shadow(@string);
  }

  100% {
    .box-shadow(0 5px 8px 0 rgba(0, 0, 0, 0.36));
  }
}

.widget:hover {
  .box-shadow(0 5px 8px 0 rgba(0, 0, 0, 0.36));
  animation: widgethover 0.75s ease-in-out;
  -webkit-animation: widgethover 0.75s ease-in-out;
  -moz-animation: widgethover 0.75s ease-in-out;
  -ms-animation: widgethover 0.75s ease-in-out;
  -o-animation: widgethover 0.75s ease-in-out;
}
// New Hamburger menu
@keyframes slideonicon {
  from {
    left: -60px;
  }

  to {
    left: 20px;
  }
}
@-webkit-keyframes slideonicon {
  from {
    left: -60px;
  }

  to {
    left: 20px;
  }
}
@-moz-keyframes slideonicon {
  from {
    left: -60px;
  }

  to {
    left: 20px;
  }
}
@-o-keyframes slideonicon {
    from {left: -60px;}
