@keyframes overshoot {
  10% {
    transform: translate3d(0, -1.5px, 0);
  }
  80% {
    transform: translate3d(0px, 0, 0);
  }
  90% {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes overshoot-rev {
  10% {
    transform: translate3d(0, 1.5px, 0);
  }
  80% {
    transform: translate3d(0px, 0, 0);
  }
  90% {
    transform: translate3d(0, 0, 0);
  }
}
