.slide-out-pane {
  top: 40px;
  z-index: 20;
  width: 400px;
  min-width: 400px; /* Changed from width as min-width fixes the width in flex */
  height: calc(100% - 66px);
  right: -48rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0 1rem;
  position: fixed;
  box-sizing: border-box; /* Ensure the widths are accurate and not including padding and border on top of defined widths */
  background: @white;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-box-shadow: -6px 0 2px 0 rgba(0, 0, 0, .4);
          box-shadow: -6px 0 2px 0 rgba(0, 0, 0, .4);

  &.affix {
    top: 48px;
    height: calc(100% - 48px);
  }

  &:not(.docked) {
    -webkit-transition: right .2s linear;
    transition: right .2s linear;
  }

  &.docked {
    // -webkit-box-flex: 1;
    //     -ms-flex: 1;
    //         flex: 1;
    top: 0;
    height: 100%;
    position: fixed;
    -webkit-box-shadow: none;
            box-shadow: none;
    position: relative;
    display: inline-block;
    z-index: 0 !important;
    border-left: 1px solid @mediumgray;

    &.visible {
      position: relative;
    }

    &:not(.visible) {
      display: none;
    }

    .pane-dock {
      .opacity(0.87)!important;
    }
  }

  &.large-pane {
    min-width: 800px; /* Changed from width as min-width fixes the width in flex */
    right: -81rem;

    .q-content_Expansion {
      -webkit-transform: rotateY(180deg);
              transform: rotateY(180deg);
    }
  }

  &.visible,
  &.visible.large-pane {
    right: 0;
    z-index: 15;
  }

  .k-splitbar {
    width: 100% !important;

    &.custom-border {
      border-top: 1px solid @mediumgray;
    }
  }

  table.auto-resize {
    width: 100%;
    height: 100%;
    border-width: 0;
  }

  .pane-content {
    height: 100%;
  }

  .top-wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-top: 1rem;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column nowrap;
            flex-flow: column nowrap;

    .title-actions {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-flow: row nowrap;
              flex-flow: row nowrap;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
    }

    .actions button {
      border: none;
      padding: 0;
      .opacity(0.54);
      margin-right: 1rem;
      background-color: transparent;

      &:hover {
        .opacity(0.84);
        cursor: pointer;
      }

      &:last-child {
        margin-right: 0;
      }
    }

    .filters {
      width: 100%;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;

      > span {
        margin-right: 2rem;

        &:last-child {
          margin-right: 0;
        }
      }

      .k-dropdown {
        margin-top: 1rem;
      }
    }
  }

  .submit-wrapper {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-flow: row nowrap;
            flex-flow: row nowrap;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
  }

  @media (max-width: @screen-lg-min) {
        .pane-resize {
          display:none; // Ensure resizing pane is not available on smaller screens
        }
      }
  
      @media (max-width: @screen-md-min) {
        .pane-dock {
          display:none; // Ensure resizing pane is not available on smaller screens
        }
      }    
}