.radio label {
	cursor: pointer;
	padding-left: 34px;
	position: relative;
}

.radio label span {
	display: block;
	position: absolute;
	left: 3px;
	top: 2px;
	transition-duration: 0.2s;
}

.radio input[type=radio] {
	height: 15px;
	width: 15px;
	border-radius: 100%;
	background-color: rgba(0, 0, 0, 0.84);
	transform: scale3d(0, 0, 0);
}

.radio input[type=radio]:checked+label:after {
	display: block;
	position: absolute;
	content: "";
	background-color: rgba(0, 0, 0, 0.84);
	left: -18px;
	top: -18px;
	height: 50px;
	width: 50px;
	border-radius: 100%;
	z-index: 1;
	opacity: 0;
	margin: 0;
	transform: scale3d(1.5, 1.5, 1);
}

.k-checkbox-label.pull-left {
	position: relative;
	float: left;
	left: auto;
}

.k-checkbox-label.pull-left:after {
	left: auto;
}

.k-checkbox-label.pull-left:after {
	color: @mid-blue;
	left: auto;
}

input[type="checkbox"]:checked+ input[type='hidden']+ label.pull-left:after {
	content: "\2713";
	width: 1em;
	height: 1em;
	position: absolute;
	left: auto;
	border-width: 1px;
	border-style: solid;
	text-align: center;
	background-color: @dashblue;
}

.k-checkbox-label.pull-left:before {
	border: none;
	box-shadow: none;
	color: @dashblue;
}

.k-checkbox:focus + .k-checkbox-label.pull-left:before {
	border-color: none;
	box-shadow: none;
	border: none;
	color: @dashblue;
}

