// Open Ended Date Picker
.datepicker-openended {
	.k-datepicker, .k-timepicker {
		width: calc(100% - 22px)!important;
	}
	.k-datetimepicker {
		width: calc(100% - 30px)!important;
	}

	.btnopenended {
		display: inline-block;
		border: 0!important;
		background-color: transparent!important;
		position: absolute;
		right: 0;
        top: 20px;
        box-sizing: border-box;
		.opacity(0.54);

		&:hover {
			.opacity(0.87);
		}
	}

	.inactivedatebtn {
		.opacity(0.27);

		&:hover {
			.opacity(0.87);
		}
	}
}

// Open Ended Date Picker -- Flex

.q-form-control-flex {
  display: flex;
  flex-flow: row nowrap;
  /* padding: 5px; */

  .picklistAddition {
  padding-top: 20px;
  float: none;
}
&.singlepick .datepicker-openended {
  width: calc(100% - 30px) !important;
}

&:not(.singlepick) > div:nth-child(1) {
  margin-right: 20px;
}

& > div:nth-child(1),
& > div:nth-child(2) {
  flex-grow: 1;
}
& > div:nth-child(1) {
  padding-right: 15px;
}
& > div:nth-child(2) {
  padding-left: 15px;
}
}

@media (max-width: 768px) {
  .q-form-control-flex {
    flex-flow: row wrap;

	  & > div:nth-child(-n+2) {
	    width: 100%;
	  }

	  &.singlepick  > div:nth-child(-n+2) {
	    width: calc(100% - 30px);
	  }
	}
}


.k-grid {
	.datepicker-openended {
		.k-datepicker, .k-timepicker {
			width: calc(100% - 22px)!important;
		}

		.k-datetimepicker {
			width: calc(100% - 30px)!important;
		}

		.btnopenended {
			display: inline-block;
			border: 0!important;
			background-color: transparent!important;
			position: absolute;
			right: 0;
			top: 5px;
			.opacity(0.54);
		}
	}
}
// Field alignment
.fieldAlignRight {
	input {
		text-align: right!important;

		&::-webkit-input-placeholder {
			text-align: left!important;
		}

		&::-moz-placeholder {
			text-align: left!important;
		}

		&:-moz-placeholder {
			// Older versions of Firefox
			text-align: left!important;
		}

		&:-ms-input-placeholder {
			text-align: left!important;
		}
	}
}

.fieldAlignCenter {
	input {
		text-align: center!important;

		&::-webkit-input-placeholder {
			text-align: left!important;
		}

		&::-moz-placeholder {
			text-align: left!important;
		}

		&:-moz-placeholder {
			// Older versions of Firefox
			text-align: left!important;
		}

		&:-ms-input-placeholder {
			text-align: left!important;
		}
	}
}
// Numeric alignment
.numericAlignRight {
	input {
		text-align: right!important;

		&::-webkit-input-placeholder {
			text-align: left!important;
		}

		&::-moz-placeholder {
			text-align: left!important;
		}

		&:-moz-placeholder {
			// Older versions of Firefox
			text-align: left!important;
		}

		&:-ms-input-placeholder {
			text-align: left!important;
		}
	}
}

.numericAlignCenter {
	input {
		text-align: center!important;

		&::-webkit-input-placeholder {
			text-align: left!important;
		}

		&::-moz-placeholder {
			text-align: left!important;
		}

		&:-moz-placeholder {
			// Older versions of Firefox
			text-align: left!important;
		}

		&:-ms-input-placeholder {
			text-align: left!important;
		}
	}
}
// Force all IE placeholders to align left
.fieldAlignRight input:-ms-input-placeholder {
	text-align: left!important;
}

:-ms-input-placeholder {
	text-align: left!important;
}
// Remove IE's automatic cross additions to clear inputs
::-ms-clear {
	display: none;
	width: 0;
	height: 0;
}

