//
// Quorum Typography using System Fonts as a base
// --------------------------------------------------
html {
    font-family: @font-family-sans-serif
}

// Text classes
.disclaimer {
	color: @brand-danger;
}

.footerlink a {
    font-size: 12px;
    color: @secondarytext;
    text-transform: uppercase;
    font-weight: 300;
}

h1,
h2,
h3,
h4,
h5 {
    margin-bottom: 0 !important;
}

.material h1:first-child,
.material h2:first-child,
.material h3:first-child,
.accordion+h2,
.accordion+h3,
.accordion+h1 {
    margin-top: 6px;
    margin-bottom: 4px !important;
}

h2.guide {
    margin: 64px 0 32px 0 !important;
}

p,
span,
h1,
h2,
h3,
h4,
h5 {
    color: @primarytext;
}

.secondary {
    color: @secondarytext;
}

.hint {
    color: @hinttext;
}

.error {
	color: @brand-danger !important;
}

.warning {
	color: @brand-warning !important;
}

.message {
	color: @graytextmid !important;
}

.error,
.message,
.warning {
	cursor: pointer !important;
}

.disabledlink {
	color: @hinttext !important;
	cursor: default !important;
}

.totals > span {
	font-weight:bold;
	color: @primarytext !important;
}

.totals > p {
	color: @secondarytext !important;
}

.intro p {
	color: @secondarytext;
	margin: 8px 0;
}

.q-bold {
	font-weight:bold !important;
	color: @primarytext !important;
}

.q-bolditalic {
    font-weight: bold !important;
    font-style: italic !important;
    color: @primarytext !important;
}

.q-italic {
	font-style: italic !important;
	color: @primarytext !important;
}


a.webstdlink {
    color: @innovationblue !important;

    &:hover, &:focus {
         color: @innovationblue !important;
         text-decoration: underline !important;
    }
}
