.carouselExpand {
    .carousel-inner {
        min-height: 120px;
        height: auto!important;
    }
    .notice-body {
        font-size: 12px;
    }
    .notice-body:not(.notice-body-hidden) {
        -webkit-transition: max-height 2s; 
        -moz-transition: max-height 2s; 
        -ms-transition: max-height 2s; 
        -o-transition: max-height 2s; 
        transition: max-height 2s; 
        overflow: hidden; 
        max-height: 1100px;
        padding-bottom: 32px;
        font-size: 12px;
        color: #333;
        margin-bottom: 32px;
        p {
            margin: 0 0 12px 0;
        }
    }
    .notice-body-hidden {
        max-height: 26px; 
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        height: 20px;
        flex: 1;
    }
    .notice-flex {
        display: flex;
    }
    .notice-view-link {
        flex: 0 0 90px;
        font-size: 12px;
        color: #047bc1;
    }
    .notice-body:not(.notice-body-hidden) + .notice-view-link {
        display: none;
    }
    .additional-link {
        margin-top: 12px;
        font-size: 12px;
        color: #047bc1;
        display: block;
    }
    .notice-body.notice-body-hidden .additional-link {
        display: none;
    }
    .masterlink:hover {
        cursor: pointer;
    }
}