.errorlog {
  h3 {
    margin-bottom: 6px!important;
    margin-top: 12px!important;
  }

  h4 {
    margin-bottom: 12px!important;
    margin-top: 8px!important;
  }

  p {
    overflow-wrap: break-word;
  }
}

.errorlog li:nth-child(odd) {
  background: @slightgray;
  padding: 6px 12px;
}

.errorlog-noerrors {
  width: 100%;
  text-align: center;
  padding-top: 24px;
  .opacity(0.54);
}
