@-webkit-keyframes flash {
  0% { background-color: transparent; }
  30% { background-color: @notes-selected; }
  100% { background-color: transparent; }
}

@keyframes flash {
  0% { background-color: transparent; }
  30% { background-color: @notes-selected; }
  100% { background-color: transparent; }
}

