  .upload-from.recent-attachments-wrapper {
    display: none;

    .recent-attachments-list {

      .recent-attachments-list-headers {
        color: @menulink;
        padding: 1rem 0;
        border-bottom: 1px solid @grayhighlight;
      }

      .recent-attachment-wrapper {
        cursor: pointer;
        padding: 0.5rem 0;
        position: relative;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        min-height: calc(45px - 1rem);
        border-bottom: 1px solid @grayhighlight;

        &:not(:last-child) {
          border-bottom: 1px solid @grayhighlight;
        }

        .radio {
          width: 15px;
          height: 15px;
          // -ms-grid-column: 1;
          // -ms-grid-column-span: 1;
          // grid-column: 1 / 2;
          border-radius: 100%;
          justify-self: center;
          border: 2px solid @lighter-black;
          -webkit-box-shadow: 0 0 0 2px rgba(0, 0, 0, 0);
          box-shadow: 0 0 0 2px rgba(0, 0, 0, 0);
        }

        &.active {
          .opacity(1);
          background-color: @notes-selected;

          .radio {
            border: 2px solid @white;
            background-color: @dashblue;
            -webkit-box-shadow: 0 0 0 2px @dashblue;
            box-shadow: 0 0 0 2px @dashblue;
          }
        }

        &.expanded {
          -webkit-box-align: start;
          -ms-flex-align: start;
          align-items: flex-start;
          background-color: @notes-selected;
        }
      }
    }
  }

  &.large-pane {
    .hide-in-large {
      display: none;
    }

    .attachment-wrapper {
      .postedBy,
      .comment {
        display: block;

        > span {
          display: none !important;
        }
      }
    }
    .expanded {
      .side-by-side-in-large {
        width: 33.3%;
      }
    }
  }

  .submit-wrapper {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    border-top: 1px solid @grayhighlight;
    padding: 1rem 0;

    .submit-childarea1,
    .submit-childarea2 {
      flex: 1;
      overflow-x: hidden;
      padding-right: 1rem;
      text-align: center;
    }
  }
 // Prevents hiding of required field 
#DocAttachClaim {
  padding-left: 8px;
}

  .add-attachments-pane, .edit-attachments-pane {
    bottom: 0;
    display: none;
    padding-top: 1rem;
    position: absolute;
    background: @white;
    width: calc(100% - 2rem);
    height: calc(100% - 4rem);
    border-top: 1px solid @grayhighlight;
    overflow-y: auto;
    z-index: 1;

    h4.file-heading {
      display: inline-block;
    }

    .filetype {
      .opacity(0.54);
    }

    .k-upload-files {
      line-height: normal!important;

      .k-file .q-form-control-label {
        display: block;
      }
    }

    .file-wrapper {
      margin-bottom: 1.5rem;
    }
  
    .title-filter {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
      -ms-flex-pack: justify;
      justify-content: space-between;
      cursor: pointer;
    }

    .create-buttons {
      display: grid;
      padding-top: 1rem;
      -ms-grid-columns: 33% 34% 33%;
      grid-template-columns: 33% 34% 33%;

      button {
        .opacity(1);
        padding: 1rem;
        background: none;
        border: 1px solid @gray-light;

        &.active {
          color: @white;
          border: @dashblue;
          background-color: @dashblue;
        }

        &:first-child {
          border-radius: .5rem 0 0 .5rem;
        }

        &:last-child {
          border-radius: 0 .5rem .5rem 0;
        }

        &:not(:last-child) {
          border-right: none;
        }
      }
    }

    .attachment-form {  

      .k-widget {
        margin-bottom: 8px;
      }
      .save-attachment {
        width: 8rem;
        float: right;
      }

      .hidden-file-input {
        display: none;
      }

      input,
      textarea {
        width: 100%;
      }

      .button-wrapper {


        button {
          border: none;
          color: @dashblue;
          padding: 1rem 2rem;
          background-color: @white;
          text-transform: uppercase;

          &[type="submit"] {
            color: @white;
            border-radius: .5rem;
            background-color: @dashblue;
          }

          &:not(:last-child) {
            margin-right: 1rem;
          }
        }
      }
    }
  }
    // Move filters to the left
    .k-header > .k-grid-filter,
    .k-header > .k-header-column-menu {
      float: none;
      display: inline-block;
    }
    .k-grid-header th.k-header > .k-link {
      display: inline-block;
      padding-left: 1rem;
    }
    .k-grid-header th.k-header {
      padding-left: 0;
    }
    span.k-icon.k-i-filter {
      margin-top: -10px;
    }
    td.comment div {
      height: 18px;
      overflow:hidden;
    }
    .k-editable-area .k-tooltip-validation {
      position: absolute;
      left: 0;
      bottom: -30px;
      background: none;
      color: @red-alert;
        span.k-icon {
          display: none;
        }
    }
    // Hide the default attachment message as we are using a custom one
  .k-editable-area .k-widget.k-tooltip.k-tooltip-validation.k-invalid-msg {
    display: none!important;
  }
}

/* Kendo upload bar */
.k-progress-override {
  height: 6px !important;
  border-radius: 6px !important;
  background-color: #49b9fb !important;
  margin: 0 0 0 8px;
  max-width: 98%;
}