
.k-treelist .k-state-selected, .k-treelist .k-state-selected td, .k-treelist .k-alt.k-state-selected, .k-treelist .k-alt.k-state-selected > td {
	background-color: lighten(@grayborder,5%)!important;
}

.treelist {

	margin-bottom:4px;

	.k-icon.k-i-none {
		width:14px;
		height:14px;
	}


	 .k-i-expand {

	    .q-grid_expand!important;
	    width: 14px !important;
	    height: 14px !important;
}

	 .k-i-collapse {

	    .q-grid_collapse!important;
	    width: 14px !important;
	    height: 14px !important;
}

.k-i-expand, .k-i-collapse {
	&:hover {
		cursor:pointer!important;
	}

}

}

.proc-icon {
    display: inline-block;
    width: 18px;
    height: 18px;
    border-radius: 2px;
    margin-left: 12px;
    &:before {
		content: "";
		margin: 4px 0 0 4px;
		.sprite;
	}
}


.proc-step {
    border-radius: 10px;
}



.proc-cat-queued {
    background-color: @grayOnGrid;
     &:before {
    	.q-small_WaitingForAnotherPersonToTakeAction;
    		.opacity(0.54);
    }
}

.proc-cat-processing {
    background-color: @grayOnGrid;
     &:before {
    	.q-small_PendingHourglass;
    	.opacity(0.54);
    }
}

.proc-cat-completed {
    background-color: @brand-completed;
}

.proc-cat-suspended {
    background-color: @brand-danger;
     &:before {
    	.q-smallw_Denied;
    }
}

// specific status color overrides - todo: replace with icons

.proc-i-UNK {
    background-color: @brand-warning;
    color: white;
    text-align: center;
    &:before {
    	.q-smallw_Unknown;
    }
}

.proc-i-CS {
    background-color: @brand-success; // green
    &:before {
    	.q-smallw_Done;
    }
}

.proc-i-CW {
    background-color: @brand-warning; // yellow
     &:before {
    	.q-small_Warning;
    }
}

.proc-i-CE, .proc-i-CSE {
    background-color: @brand-danger; // red
     &:before {
    	.q-smallw_Cancel;
    }
}
