@media (max-width: @screen-sm-max) {
  ul#LauncherPanelBar {
    li.k-first,
    li.k-last {
      display: none;
    }
  }
}

.k-panelbar .k-panel,
.k-panelbar.k-header {
  border-color: @white !important;
}

.k-pager-wrap {
  border-color: transparent!important;
}

.side-panel {
  margin-top: 9px;

  .q-form-control-wrapper {
    background: transparent!important;
    border-left: none!important;
    border-right: none!important;
    width: 80%!important;
    margin: 0 16px!important;
  }

  .k-panelbar {
    background-color: @white;

    .k-item {
      border-color: @white !important;
    }

    .k-header {
      border-top: 1px solid blue!important;
      border-color: @white !important;
    }

    .k-item,
    .k-item > .k-header {
      background: transparent!important;
    }

    .k-item > .k-link {
      color: @primarytext!important;
      font-size: 15px!important;
      padding: 8px 16px!important;
    }

    .k-item > .k-header {
      padding-top: 0;
      padding-bottom: 0;
      border-bottom: 1px solid rgba(0, 0, 0, 0.27)!important;
      border-top: 0!important;
    }

    .k-item > .k-header {
      font-size: 18px;
      padding: 8px 16px!important;
    }

    .k-item .count {
      background: transparent!important;
      float: right;
      color: @secondarytext!important;
    }

    & > li > .k-state-selected {
      font-weight: normal!important;
    }

    .k-i-arrow-n {
      .q-content_collapse!important;
    }

    .k-i-arrow-s {
      .q-content_expand!important;
    }

    .k-panelbar-collapse,
    .k-panelbar-expand {
      position: absolute;
      top: 10px;
    }

    li.k-item.k-state-default.k-last {
      margin-bottom: 24px;
    }
  }
}
