.k-more-events.k-button {
  position: absolute!important;
}

.k-scheduler-toolbar .k-link {
  color: @white!important;
}

.k-scheduler li.k-state-default a.k-link {
  color: @white !important;
}

.k-scheduler .k-nav-current.k-state-hover .k-link .k-icon.k-i-calendar,
.k-scheduler .k-nav-current.k-state-hover .k-link .k-icon.k-i-calendar:hover {
  .q-contentw_Calendar!important;
}

.k-event {
  .opacity(0.90);
}

.k-scheduler {
  .k-i-calendar,
  .k-i-calendar:hover {
    .q-contentw_Calendar!important;
  }

  .k-si-close {
    .q-contentw_Delete!important;
    .opacity(1)!important;
  }

  .k-i-arrow-e {
    .q-contentw_arrow_right!important;
    .opacity(1)!important;
  }

  .k-i-arrow-w {
    .q-contentw_arrow_left!important;
    .opacity(1)!important;
  }

  .k-i-arrow-n {
    .q-contentw_arrow_up!important;
    .opacity(1)!important;
  }

  .k-i-arrow-s {
    .q-contentw_arrow_down!important;
    .opacity(1)!important;
  }

  .k-i-refresh,
  .k-i-reload {
    .q-contentw_Refresh!important;
    .opacity(1)!important;
  }

  .k-header.k-scheduler-footer ul.k-header li {
    border-color: @dashblue;
  }

  .k-scheduler-table th {
    color: @secondarytext;
  }

  .k-nav-current span {
    color: @white;
  }

  .k-scheduler-toolbar > ul > li:first-child,
  .k-scheduler-toolbar > ul.k-scheduler-views > li:first-child + li {
    border-left-width: 0!important;
  }

  .k-scheduler-views .k-state-selected > .k-link {
    color: @white!important;
  }
}

.k-scheduler-toolbar > .k-scheduler-views > li.k-state-selected {
  border-bottom-color: lighten(@dashblue,25%)!important;
}

.k-scheduler-views li.k-state-hover {
  border-color: darken(@dashblue,25%)!important;
}


.k-event,
.k-scheduler .k-header,
.k-scheduler .k-header .k-link,
.k-scheduler .k-header li,
.k-task-complete {
  background-color: @dashblue!important;
  border-color: @dashblue;
}

.k-edit-form-container .k-edit-buttons {
  border-style: hidden;
}

.k-edit-form-container .k-edit-buttons {
  background: @white;
}
@media only screen and (max-width: 480px) {
  .k-mobile {
    .k-scheduler-toolbar > ul:first-child {
      float: none;
    }

    .k-scheduler-toolbar > ul.k-scheduler-views {
      position: relative;
      right: inherit;
      top: inherit;
    }

    .k-scheduler-toolbar li.k-nav-current .k-sm-date-format {
      display: inline-block!important;
    }
  }
}
@media only screen and (max-width: 1024px) {
  .k-scheduler-toolbar > ul.k-scheduler-views {
    top: 0!important;
  }
}
// Shipping scheduler

.k-animation-container .k-tooltip {
  color: @primarytext!important;
  background: @white!important;
  font-size: @gridfont;
  text-transform: none!important;
  z-index: 100!important;
  .box-shadow(1px 1px 7px 4px rgba(128, 128, 128, 0.84))!important;
  background-color: @white !important;
}

.content-calendar {
  padding: 0 0 0 3px;
}

.quorumcalendar {
  .k-scheduler {
    border-color: transparent;
    background: transparent;
    margin-top: -1px;
  }

  .k-scheduler-content,
  .k-scheduler-header {
    .k-event {
      background: transparent!important;
      border-color: transparent!important;
      margin-top: 4px;
      text-align: center;

      h4 {
        margin-top: 0!important;
        font-weight: bold;
        color: @white;
        font-size: 12px;
        padding: 4px!important;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        a {
          color: @white;
        }
      }
    }

    .event-red {
      background-color: #b71c1c;
      border: 1px solid darken(#b71c1c,10%);
    }

    .event-holiday,
    .event-none {
      background-color: @white;
      border: 1px solid lighten(@gray,10%);

      h4 {
        color: @secondarytext;

        a {
          color: @secondarytext;
        }
      }
    }

    div.event-holiday + span.k-event-actions,
    div.event-none + span.k-event-actions {
      .k-si-close {
        .q-content_Delete!important;
      }
    }

    .event-green {
      background-color: @green;
      border: 1px solid darken(@green,10%);
    }

    .event-gray {
      background-color: @gray;
      border: 1px solid darken(@gray,10%);
    }

    .event-yellow {
      background-color: @yellow;
      border: 1px solid darken(@yellow,10%);

      h4 {
        a {
          color: @secondarytext;
        }
      }
    }

    .event-blue {
      background-color: @dashblue;
      border: 1px solid darken(@dashblue,10%);
    }

    .event-warning {
      background-color: @brand-warning;
      border: 1px solid darken(@brand-warning,10%);
    }

    .event-error {
      background-color: @brand-danger;
      border: 1px solid darken(@brand-danger,10%);
    }

    .k-event-actions {
      top: 0!important;
    }

    .k-today {
      border: 2px solid @dashblue;
      background: @white;

      .k-nav-day {
        font-weight: bold;
        color: @dashblue;
      }
    }
  }
}
// Scheduler
.k-scheduler-edit-form .k-edit-form-container .k-combobox,
.k-scheduler-edit-form .k-edit-form-container .k-datepicker,
.k-scheduler-edit-form .k-edit-form-container .k-datetimepicker,
.k-scheduler-edit-form .k-edit-form-container .k-dropdown,
.k-scheduler-edit-form .k-edit-form-container .k-input,
.k-scheduler-edit-form .k-edit-form-container .k-textbox,
.k-scheduler-edit-form .k-edit-form-container .k-timepicker {
  border: none;
  border-radius: 0;
  outline: none;
  cursor: pointer;
  background-color: @white;
  background-repeat: no-repeat;
  background-position: center bottom;
  width: 100%;
}

.k-scheduler-edit-form .k-edit-form-container,
.k-scheduler-edit-form .k-edit-form-container {
  .k-input,
  .k-textbox {
    background-image: linear-gradient(@mediumgray, @mediumgray)!important;
    background-size: 100% 0.143rem!important;
    border: none;
    outline: none;
    box-shadow: none;
    background-color: transparent;
    width: 100%;
  }
}

.k-scheduler-edit-form .k-edit-form-container {
  .k-dropdown-wrap.k-state-focused,
  .k-dropdown-wrap.k-state-hover,
  .k-picker-wrap.k-state-focused,
  .k-picker-wrap.k-state-hover {
    .k-input,
    .k-textbox {
      background-image: linear-gradient(@dashblue, @dashblue), linear-gradient(@mediumgray, @mediumgray)!important;
      background-size: 100% 2px, 100% 1px!important;
      border: none;
      outline: none;
      box-shadow: none;
      background-color: transparent;
      width: 100%;
    }
  }
}

.k-scheduler-edit-form .k-edit-form-container .k-input,
.k-scheduler-edit-form .k-edit-form-container .k-input.k-textbox,
.k-scheduler-edit-form .k-edit-form-container .k-textbox {
  &:focus,
  &:hover {
    background-image: linear-gradient(@dashblue, @dashblue), linear-gradient(@mediumgray, @mediumgray)!important;
    background-size: 100% 2px, 100% 1px!important;
    border: none;
    outline: none;
    box-shadow: none;
    background-color: transparent;
    width: 100%;
  }
}

.k-scheduler-edit-form .k-edit-form-container .k-picker-wrap,
.k-scheduler-edit-form .k-edit-form-container .k-picker-wrap.k-state-focused.k-state-hover {
  background-color: @white !important;
}

.k-scheduler-edit-form .k-edit-form-container .k-edit-field .k-check,
.k-scheduler-edit-form .k-edit-form-container .k-edit-field input[type='radio'] {
  padding: 7px;
  margin: 5px;
}

.k-scheduler-edit-form .k-edit-form-container .k-edit-field {
  .k-numeric-wrap.k-state-hover,
  .k-select:hover {
    background-color: @white !important;
  }

  .k-numeric-wrap.k-state-focused,
  .k-select:focus {
    background-color: @white !important;
  }

  .k-state-default,
  .k-state-focused,
  .k-state-hover {
    -webkit-appearance: none!important;
    border: none!important;
    outline: none!important;
    border-color: transparent!important;
    background-color: transparent!important;
    border-style: none!important;
    border-width: 0!important;
    -webkit-box-shadow: none!important;
    box-shadow: none!important;
  }
}

.k-edit-field > input[type="checkbox"],
.k-edit-field > input[type="radio"] {
  padding: 0;
  margin-top: 0.7em!important;
}

.k-dropdown-wrap.k-state-default,
.k-numeric-wrap.k-state-default,
.k-picker-wrap.k-state-default {
  border-color: transparent!important;
}

.k-picker-wrap .k-select .k-icon {
  .opacity(0.54);
}

.k-picker-wrap .k-select .k-icon:hover {
  .opacity(0.87);
}

.k-dropdown .k-state-default,
.k-numeric-wrap.k-state-default {
  border-color: transparent;
  background-color: transparent;
}

.k-state-disabled {
  border-color: transparent;
}

.k-picker-wrap.k-state-default > .k-select {
  border-color: transparent;
  background-color: transparent;
}

.norowpadding > div {
  padding-left: 0;
  padding-right: 0;
}

.norowpadding {
  padding-left: 15px;
}

.q-lockCover {
  .opacity(0.25);
  background: @white;
}
