@import "_sprites.less"; // Processed by Grunt SpriteSmith

// 2x version available but at this stage may be more reliable to only use 1x making use of 2x graphic
// @import "_sprites.less";@import "_sprites-2x.less";

@version: '3';
@url: %("images/sprites-2x.png?v=1%s", @version);

// Created a separate loop to fix bug in SpriteSmith - syntax in recalling loop was incorrect, needed additional parenthesis

.sprites2(@sprites2, @i: 1) when (@i <= length(@sprites2)) {
  @sprite: extract(@sprites2, @i);
  @sprite-name2: e(extract(@sprite, 10)); // ensure this is escaped otherwise inverted commas show in icon values
  .@{sprite-name2} {
	//.sprite(@sprite);
	.sprite-position(@sprite); // Only return the position in each sprite class
  }
  .sprites2(@sprites2, (@i + 1));
}

// Do not call the .sprites that SpriteSmith generates in the LESS file, contains a bug
.sprites2(@spritesheet-sprites);

.sprite {
	background-size: @q-content-lock-total-width @q-content-lock-total-height!important;
	display: inline-block;
	background-image: url(@url)!important;
}

// Kendo Icon overrides
// Update version info below
.k-icon:not(.k-tool-icon):not(.k-i-more-vertical) {
	background-image: url(@url)!important;
	background-size: @q-content-lock-total-width @q-content-lock-total-height!important;
}

