.q-wizard.material, .q-wizard .material {
    box-shadow: none;
    border: 1px solid #d0d0d0;
    position: relative;
    min-height: 288px;
}
@media (min-width: 801px) {
    .q-wizard.material {
        &.q-wizard--intro--active {
            height: calc(100vh - 194px);
        }
        &:not(.q-wizard--intro--active){
            height: calc(100vh - 132px);
        }
    }
}

.q-wizard--header {
    display: flex;
    background-color: #eee;
    padding: 1em;
    min-height: 60px;
    p {
        margin: auto;
    }
    h2 {
        font-size: 19px;
        margin-right: 20px;
    }
}

@media (max-width: 800px) {
    .q-wizard--header {
        display: block;
        height: auto;
    }
}

.q-wizard--body {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: row wrap;
    height: 100%;
    flex: 1;
}

.q-wizard--end,
.q-wizard--start {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 0;
    opacity: 0;
    display: none;
}

.q-wizard--end.q-wizard--end--active,
.q-wizard--start.q-wizard--start--active {
    position: relative;
    z-index: 1;
    opacity: 1;
    display: block;
}

.q-wizard--option {
    flex: 1;
    opacity: 0;
    border: 1px solid #ccc;
    border-right: none;
    padding: calc(50vh - 193px) 20px 0;
    height: calc(100vh - 193px);
    transition: all 0.25s ease-in-out;
    &:nth-child(1) {
        border-left: none;
    }
    &:last-child {
        border-right: none;
    }
    &:hover {
        cursor: pointer;
        background-color: #eee;
    }
    .q-wizard--icon {
        background-color: #057ac2;
        padding: 22px;
        border-radius: 200px;
        width: 62px;
        height: 62px;
        margin: 7px auto;
    }
    h3,
    p,
    a {
        text-align: center;
        display: block;
    }
    p {
        margin-top: 40px;
    }
}

.q-wizard--intro--active {
    .q-wizard--option:not(.q-wizard--option--success){
        padding: calc(50vh - 256px) 20px 0;
        height: calc(100vh - 254px);
    }
}

.q-wizard--end .q-wizard--option:not(.q-wizard--option--success):not(.q-wizard--option--noresult) {
    height: auto;
    padding: calc(50vh - 280px) 20px 0;
    height: calc(100vh - 363px);
}

.q-wizard--intro--active .q-wizard--end {
    .q-wizard--option:not(.q-wizard--option--success):not(.q-wizard--option--noresult) {
        padding: calc(50vh - 303px) 20px 0;
        height: calc(100vh - 393px);
    }
}

@media (max-height: 484px) {
    .q-wizard--end .q-wizard--option:not(.q-wizard--option--success):not(.q-wizard--option--noresult) {
        padding: 40px 20px;
        min-height: auto;
        height: auto;
    }
    .q-wizard--end.q-wizard--intro--active {
        .q-wizard--option:not(.q-wizard--option--success):not(.q-wizard--option--noresult) {
            padding: 40px 20px;
            min-height: auto;
            height: auto;
        }
    }
    .q-wizard--intro--active .q-wizard--option:not(.q-wizard--option--success) {
        height: auto;
        padding: 20px 10px; // min-height: 480px;
    }
    .q-wizard--option:not(.q-wizard--option--success) {
        height: auto;
        padding: 20px 10px; // min-height: 480px;
    }
}

@media (max-width: 800px) {
    .q-wizard--intro--active .q-wizard--option:not(.q-wizard--option--success),
    .q-wizard--intro--active .q-wizard--option {
        padding: 20px;
        height: auto;
    }
    .q-wizard--option {
        flex: none;
        width: 100%;
        padding: 20px;
        height: auto;
        border-left: none;
        border-top: none;
        &:active,
        &:focus {
            background-color: #eee;
        }
        p {
            margin-top: 10px;
        }
    }
    .q-wizard--header {
        border-bottom: 1px solid #ccc;
    }
    .q-wizard--end .q-wizard--option:not(.q-wizard--option--success):not(.q-wizard--option--noresult) {
        padding: 40px 20px;
        height: auto;
    }
    .q-wizard--end.q-wizard--intro--active .q-wizard--option:not(.q-wizard--option--success):not(.q-wizard--option--noresult) {
        padding: 40px 20px;
        height: auto;
    }
}

.q-wizard--start.q-wizard--start--active .q-wizard--option,
.q-wizard--end.q-wizard--end--active .q-wizard--option {
    opacity: 1;
}

.q-wizard--option--success {
    width: 100%;
    margin-bottom: 0;
    height: auto;
    min-height: auto;
    padding: 40px 0;
    border: 0;
    &:hover {
        cursor: default;
        box-shadow: none;
        background-color: #fff;
        position: relative;
        bottom: 0px;
        right: 0px;
    }
    p {
        margin-top: 10px;
    }
    a {
        color: @maincolor;
    }
    .q-wizard--icon {
        background-color: @brand-success;
    }
}

@media (max-width: 800px) {
    .q-wizard--option--success {
        border-bottom: 1px solid #ccc;
    }
}

.q-wizard--info {
    position: absolute;
    z-index: 0;
    top: 0;
    display: none;
    .q-wizard--header {
        h1 {
            flex: 1;
            min-width: 300px;
        }
        h2 {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            width: 270px;
            margin-right: 0;
            transition: width 0.25s linear;
        }
        p {
            flex: 3;
            text-overflow: ellipsis;
            max-width: 400px;
            white-space: nowrap;
            overflow: hidden;
            margin-left: 0;
            margin-right: 0;
        }
    }
    .q-wizard--body,
    .q-wizard--header {
        opacity: 0;
    }
    .q-wizard--body {
        justify-content: left;
        align-items: stretch;
        overflow: hidden;
    }
    .q-wizard--main {
        opacity: 0;
        flex: 4;
        width: 400px;
        transition: opacity 0.25s linear;
        transition-delay: 0.5s;
        position: relative;
        padding: 0 0 80px 0;
    }
    .q-wizard--main--active {
        opacity: 1;
    }
    .q-wizard--main--gray {
        background-color: #f2f2f2;
    }
    .q-wizard--sidebar {
        position: relative;
        left: -300px;
        flex: 1;
        min-width: 300px; // width: 300px;
        max-width: 300px;
        height: 100%;
        display: flex;
        flex-direction: column;
        background: #f7f7f7;
        overflow: hidden;
        transition: all 0.25s linear;
        .q-wizard--header {
            max-width: 300px;
            max-height: 60px;
            .icon {
                height: 14px;
                margin: auto 10px auto 15px;
                transform: rotate(180deg);
                opacity: 0.27;
                &:hover {
                    opacity: 0.87;
                    cursor: pointer;
                }
            }
        }
    }
    .q-wizard--sidebar--collapsed {
        flex: 0;
        width: 68px !important;
        min-width: 68px !important;
        overflow: visible;
        .text {
            opacity: 0;
        }
        .q-wizard--header {
            h2 {
                width: 0;
            }
            .icon {
                transform: rotate(0deg);
            }
        }
        ul {
            overflow: hidden;
            overflow-y: auto;
        }
    }
}

.q-wizard--info.q-wizard--info--active {
    position: relative;
    z-index: 1;
    display: flex;
    height: 100%;
    .q-wizard--body,
    .q-wizard--header {
        opacity: 1;
    }
    .q-wizard--main .q-wizard--header {
        justify-content: left;
        align-items: stretch;
    }
}

.q-wizard--header--inactive {
    display: none;
}

.q-wizard--info.q-wizard--info--active .q-wizard--sidebar {
    left: 0;
    transition: all 0.25s linear;
}

.q-wizard--sidebar ul {
    list-style: none;
    padding: 0;
    background-color: #f7f7f7;
    // height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    border: none;
    margin-bottom: 0;
    transition: height 1s linear;
    li {
        display: none;
        padding: 12px 20px;
        border: 0;
        border-bottom: 1px solid #ccc;
        min-width: 300px;
        color: #9c9c9c;
        transition: background 0.25s linear;
        &:last-child {
            border-bottom: 0;
        }
        .icon {
            width: 26px;
            height: 26px;
            border-radius: 40px;
            padding: 5px 4px 3px 7px;
            background-color: #d4d0d1;
            margin-right: 23px;
            display: inline-block;
        }
        &:hover {
            cursor: not-allowed;
        }
    }
    li .text {
        color: #9c9c9c;
        opacity: 1;
        transition: opacity 0.25s linear;
        transition-delay: 0.25s;
    }
    li.active {
        font-weight: 600;
    }
    li.active,
    li.visited,
    li.valid,
    li.error {
        border-right: 0;
        background-color: #f2f2f2;
        opacity: 1;
        color: #333333;
        .text {
            color: #333333;
        }
    }
    li.error {
        color: #333333 !important;
    }
    li.visited,
    li.active {
        &:hover {
            cursor: pointer;
            background-color: #eae9e9;
        }
    }
    li .icon .sprite {
        .q-wizard_current();
    }
    li.active .icon {
        background-color: @dashblue;
    }
    li.valid .icon {
        background-color: @brand-success;
        .sprite {
            .q-wizard_complete();
        }
    }
    li.error .icon {
        background-color: @brand-danger;
        .sprite {
            .q-wizard_error();
        }
    }
    li.active .icon {
        .sprite {
            .q-wizard_next();
        }
    }
}

.q-wizard--section {
    display: none;
    opacity: 0;
    transition: opacity 0.25s linear;
    .q-wizard--section--inputs {
        box-sizing: border-box;
        padding: 20px;
        height: calc(100vh - 300px);
        overflow-y: auto;
        overflow-x: hidden;
        .q-wizard-navigation {
            display: none;
        }
    }
}

.q-wizard--intro--active {
    .q-wizard--section {
        .q-wizard--section--inputs {
            // height: calc(100vh - 300px);
            height: calc(100vh - 336px);
        }
    }
}

@media (max-height:500px) and (min-width:799px) {
    .q-wizard--section {
        .q-wizard--section--inputs {
            height: auto;
        }
    }
}

.q-wizard--section.q-wizard--section--active {
    opacity: 1;
    display: block;
    transition: opacity 0.25s linear;
}

.q-wizard-navigation {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 20px;
    button,
    button:hover {
        color: #057ac2 !important;
        background-color: transparent !important;
        box-shadow: none !important;
    }
    button:focus {
        background-color: transparent !important;
      }
}


@media (max-width: 800px) {
    .q-wizard--section {
        .gridholder {
            overflow: scroll;
        }
    }
    .q-wizard--info {
        .q-wizard--main {
            opacity: 1;
            padding: 0;
            max-width: 100%;
        }
        .q-wizard--main--gray {
            background-color: #fff;
            .q-wizard--section--gray {
                background-color: #f2f2f2;
            }
        }
    }
    .q-wizard--section.q-wizard--section--active {
        opacity: 1;
        display: block;
        transition: opacity 0.25s linear;
    }
    .q-wizard--section {
        display: block;
        opacity: 1;
        .q-wizard--section--inputs,
        .q-wizard--section--inputs.hide {
            display: none;
            height: 0;
            transition: height 1s linear;
        }
    }
    .q-wizard--section.q-wizard--section--active .q-wizard--section--inputs {
        display: block;
        height: auto;
        padding: 20px 20px 60px 20px;
        position: relative;
        margin: 0;
    }
    .q-wizard-navigation {
        display: none;
    }
    .q-wizard--section .q-wizard--section--inputs .q-wizard-navigation {
        display: block;
        padding: 20px 20px;
        left: 0;
    }
    .q-wizard--section--active {
        .q-wizard--header--mobile {
            border-bottom: 1px solid #dedede;
        }
    }
    .q-wizard--header--mobile {
        padding: 20px;
        border-top: 1px solid #dedede;
        .icon {
            width: 26px;
            height: 26px;
            border-radius: 40px;
            display: inline-block;
            padding: 5px 4px 3px 7px;
            background-color: #d4d0d1;
            margin-right: 10px;
        }
        .text {
            color: #9c9c9c;
        }
    }
    .q-wizard--header--mobile.active {
        opacity: 1;
    }
    .q-wizard--header--mobile.active,
    .q-wizard--header--mobile.visited,
    .q-wizard--header--mobile.error,
    .q-wizard--header--mobile.valid {
        background-color: #f2f2f2;
        opacity: 1;
        .text {
            color: #333333;
        }
    }
    .q-wizard--header--mobile.error {
        color: #333333 !important;
    }
    .q-wizard--header--mobile .icon .sprite {
        .q-wizard_current();
    }
    .q-wizard--header--mobile.active .icon {
        background-color: @dashblue;
    }
    .q-wizard--header--mobile.valid .icon {
        background-color: @brand-success;
    }
    .q-wizard--header--mobile.valid .icon .sprite {
        .q-wizard_complete();
    }
    .q-wizard--header--mobile.error .icon {
        background-color: @brand-danger;
    }
    .q-wizard--header--mobile.error .icon .sprite {
        .q-wizard_error();
    }
    .q-wizard--header--mobile.active .icon .sprite {
        .q-wizard_next();
    }
    .q-wizard--info .q-wizard--sidebar {
        display: none;
    }
    .q-wizard--sidebar.q-wizard--sidebar--active {
        height: 120px;
    }
    .q-wizard--info.q-wizard--info--active .q-wizard--sidebar {
        flex: none;
        width: 100%;
    }
    .q-wizard--main {
        opacity: 1;
        background-color: #fff;
    }
    .q-wizard--main .q-wizard--header {
        display: none;
    }
}
