//
// Header
// --------------------------------------------------

.header-new .collapsed-menu-container {
	max-width: 44px;
}

header {
    height: 40px; // @media(max-width: @screen-sm) {
    // }
    h1 {
        margin-top: 6px;
        white-space: nowrap;
        width: auto;
        display: inline-block;
        overflow: hidden;
        text-overflow: unset;
        line-height: 28px;
        position: relative;
        padding: 0 12px 0 8px;
        max-width: 100%;
        vertical-align: top;
    }
    .header-text {
        width: 100%;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        display: block;
        float: left;
    }
    .mydashboard+.header-text {
        margin-left: 1rem;
        width: calc(100% - 1rem);
    }
    .mydashboard {
        position: absolute;
        left: 0;
        color: @white;
    }
    #modmark {
        // margin-left: 8px;
        color: #fff;
        // position: absolute;
        // float: left;
    }
    .header-new {
        margin: 0;
    }
    a,
    a:hover {
        text-decoration: none !important;
        outline: none;
        color: @gray-link;
    }
    .breadcrumb {
        margin-bottom: 0;
        padding: 0;
        >li {
            display: inline-block;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            a {
                color: @white;
                text-transform: capitalize;
                &:hover {
                    .opacity(1);
                }
            }
            &:before {
                content: '<';
                margin: 0 4px 0 0;
                color: #fff;
                font-size: @font-standard;
                @media(max-width: @screen-sm) {
                    padding: 0;
                    font-size: 20px;
                }
            }
        } 
    }
}

.q-actionbar {
    width: 100% !important;
    height: 36px;
    color: @white;
    position:relative;
    background-color: @midnightblue;

    .container {
        padding-right: 60px;
        @media(max-width: @screen-xs-max) {
            padding-right: 46px;
        }
    }
    button {
        background-color: transparent;
        border: 0;
        &:hover {
            color: @white;
            .opacity(1);
        }
    }
}

.actionbar-float-right {
    width:26px;
    position: absolute;
    right: 8px;
    height: 14px;
    top: 10px;
}


.q-head {
    position: static;
    border-left: 1px solid @divider;
    height: 70px;
    @media(max-width: @screen-sm) {
        height: auto;
        width: 100%;
        text-align: center;
    }
}

.wback {
    background-color: transparent;
    margin: 0 6px;
    .q-head-btn {
        position: relative;
        line-height: 40px;
        height: 40px;
        width: 100%;
        display: block;
        text-align: left;
        border: 0;
        background: transparent;
        background-image: none !important;
        cursor: pointer;
        &:after {
            display: inline-block;
            height: 8px;
            width: 4px;
            margin: 0 1rem;
            content: " ";
            border-top: 4px solid @white;
            border-right: 4px solid @dashblue;
            border-bottom: 4px solid @dashblue;
            border-left: 4px solid @dashblue;
            @media(max-width: @screen-sm) {
                display:none;
            }
        }
    }
}

.sprite.q-head_DropDown {
    display: none;
    border: 0;
    @media(max-width: @screen-sm) {
        display: block;
        margin: 1.25rem 1.5rem 0 1.5rem;
        padding: 0 !important;
        .opacity(1);
        background-color: transparent;
        float: right;
        width: 22px;
    }
}

.q-header {
    display: block;
    @media(max-width: @screen-sm) {
        display: none;
    }
    .row {
        @media(max-width: @screen-sm) {
            margin: 0;
        }
    }
}

.lopad {
    padding: 2px;
}

.alert_asterisk {
    background-image: url(images/asterisk_alert.gif) !important;
    background-size: 50px 50px !important;
    background-position: center center !important;
    background-repeat: no-repeat !important;
}

.q-dropdown-menu.dropdown-menu {
    min-width: 200px;
    padding: 1rem !important;
    background: @white;
    .box-shadow(@string);
    border-radius: 0;

    &.dropdownUsermenu,
    &.dropdownPersonamenu {
        max-height: calc(100vh - 7rem);
        overflow-y: auto;
    }
    &.dropdownMoremenu {
        max-height: calc(100vh - 12rem);
    }

    &.dropdownUsermenu,
    &.dropdownMoremenu,
    &.dropdownPersonamenu {
        .endstate {
            text-align: right;
            font-size: 12px;
            &:hover {
                background: transparent !important;
            }
        }
    }
    &.dropdownPersonamenu {
        right: 10rem;
    }
    >li {
        >a,
        button {
            height: 26px !important;
            line-height: 26px !important;
            padding: 2px 2px 2px 14px !important;
            color: @black !important;
            vertical-align: middle;
            opacity: 1;
            width: 100%;
            text-align: left;
            text-indent: 0;
            &:hover {
                color: @black;
                opacity: 1;
                background: @dropdown-fallback-border;
            }
            .sprite {
                margin-right: 1rem;
                vertical-align: middle;
            }
        }
        >button {
            &:hover {
                background: transparent !important;
            }
            &:disabled {
                .opacity(0.27);
                color: @secondarytext  !important;
                &:hover {
                    cursor: default !important;
                    color: @secondarytext  !important;
                }
            }
        }
    }
    &.dropdownUsermenu {
        li {
            height: 28px;
        }
        top: 3.25rem;
        left: auto;
        margin-left: -6rem;
        @media(max-width: @screen-sm) {
            position: static;
            display: block;
            height: auto;
            width: 100%;
            margin: 0;
            opacity: 1;
            box-shadow: none;
            max-height: 240px;
            overflow: scroll-y;
        }
        .empty {
            .opacity(0.27);
            color: @secondarytext  !important;
            font-size: 12px;
            &:hover {
                background: transparent !important;
            }
        }
    }
    &.dropdownSchedulermenu,
    &.dropdownPersonamenu {
        top: 3.25rem;
    }
    &.dropdownMoremenu {
        top: 0.3125rem;
    }
    &.dropdownSchedulermenu,
    &.dropdownPersonamenu,
    &.dropdownMoremenu {
        left: auto;
        margin-left: -6rem;
        >li {
            height: 28px;
            >a {
                height: auto;
                line-height: 26px;
                padding: 8px;
                text-indent: 1rem;
                .sprite {
                    position: absolute;
                    top: auto;
                    left: 0;
                    display: none;
                    &.q-head-selected {
                        display: inline-block;
                    }
                }
                &:hover {
                    background: transparent;
                    .sprite,
                    .q-head-selected {
                        display: inline-block;
                    }
                }
            }
        }
    }
    &.dropdownAlertsmenu {
        top: 1rem;
        right: 5rem;
        left: auto;
        min-width: 300px;
        padding: 2rem;
        color: @secondarytext;
        li {
            a {
                text-align: right;
            }
            height:auto;
            padding: 1rem 0;
            line-height:16px;
            &:first-child {
                padding-top: 0;
                padding-bottom: 0;
                height: 24px !important;
                min-height: 24px !important;
                position: relative;
            }
            &:last-child {
                padding-bottom: 0;
                text-align: right;
            }
            &:hover {
                background-color: @white!important;
                color: @secondarytext;
            }
            >a:hover {
                background-color: @white !important;
            }
        }
        h1 {
            color: @primarytext;
            margin-top: 0;
        }
        .q-head_ShowingAlerts {
            font-size: (@font-size-base - 2);
        }
        .q-head_AlertTitle {
            display: inline-block;
            width: 25%;
            vertical-align: top;
            line-height: 16px;
        }
        .q-head_AlertTitleRed {
            display: inline-block;
            width: 25%;
            color: RED;
            vertical-align: top;
            line-height: 16px;
        }
        .q-head_AlertDesc {
            display: inline-block;
            width: 74%;
            vertical-align: top;
            line-height: 16px;
            word-wrap: break-word;
        }
        a {
            display: inline-block;
            color: @dashblue;
            opacity: 1;
        }
    }
    /*&.dropdownMoremenu {
		right: 1rem;
		left: auto;
	}*/
}

.actionholderleft {
    .q-dropdown-menu.dropdown-menu li>a {
        line-height: 18px !important;
    }
    .q-dropdown-menu.dropdown-menu li button {
        line-height: 18px !important;
    }
}

.q-search {
    display: inline-block;
    width: 18px;
    height: 18px;
    margin: 11px 12px 0 16px;
    a {
        display: block;
        width: 18px !important;
        height: 18px !important;
        opacity: 1;
        .q-contentw_Search;
    }
}

.helpmenu {
    right: 1rem !important;
    left: inherit !important;
    bottom: 2rem;
    top: auto;
}

.q-help {
    margin-top: 2px;
    a.active {
        .opacity(1);
    }
}

.q-help,
.q-search {
    @media(max-width: @screen-sm) {
        display: none;
    }
}

.actionholder1,
.actionholder2,
.actionholder3,
.actionholdernaesb,
.actionholderleft {
    display: inline-block;
    // height: 36px; // Stops misalignment when secondary or tertiary actions are hidden
    text-align: right;
    white-space: nowrap;
    overflow: visible;
    button {
        &:disabled,
        &[data-disabled] {
            .opacity(0.54);
            &:hover {
                background: transparent !important;
                cursor: default !important;
            }
        }
    }
    @media(min-width: @screen-md) {
        margin-left: 2.375em;
    }
    @media(max-width: @screen-sm) {
        margin-left: 0;
    }
}

.actionholder1 {
    max-width: 400px;
    overflow-x: visible;
    margin-right: 10px;
    @media(max-width: @screen-sm) {
        max-width:120px !important;
    }
}

.actionholder2 {
    max-width: 160px;
    overflow-x: visible;
}

.actionholder3 {
    max-width: 40px;
    margin-left: 0 !important;
}

.actionholdernaesb,
.actionholderleft {
    margin-left: 32px !important;
    max-width: 40px;
    width: 40px;
}

@media(max-width: @screen-xs-max) {
    .actionholdernaesb, .actionholderleft {
        margin-left: 0 !important;
    }
}

.q-action {
    height: 36px;
    line-height: 32px;
    white-space: nowrap;
    @media(max-width: @screen-sm) {}
    &.q-action-hub {
        padding: 0 0 0 1rem;
    }
    &.q-action-add {
        padding: 0 0 0 0;
        text-align: right;
    }
    .q-action_Hub {
        max-width: 96%;
    }
    a,
    button,
    .q-action_Hub {
        display: inline-block;
        // line-height: 36px;
        height: 34px;
        margin: 0 1px;
        // padding: 6px 3px 0 3px;
        color: #fff;
        text-decoration: none;
        text-transform: capitalize;
        &:active,
        &:focus {
            background-color: darken(@midnightblue,5%);
            cursor: pointer !important;
        }
        span {
            margin-right: 2px;
            margin-left: 4px;
            color: #fff;
        }
    }
    >.q-action_Hub {
        height: 36px;
        line-height: 36px;
        margin: 0 0 0 2px;
        padding: 0 10px 0 6px;
        cursor: pointer;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        @media(max-width: @screen-sm) {
            width: 100%;
            font-size: (@font-size-base - 2);
        }
        /*&:after {
			display: inline-block;
			color: @white;
			height: 8px;
			width: 4px;
			margin: 0 1rem 0 5rem;
			content: " ";
			border-top: 4px solid @white;
			border-right: 4px solid transparent;
			border-bottom: 4px solid transparent;
			border-left: 4px solid transparent;
		}*/
        .q-hub_Warning {
            margin-right: 10px;
            vertical-align: middle;
            @media(max-width: @screen-sm) {
                margin-right: 0;
            }
        }
    }
    button {
        &:hover {
            cursor: pointer !important;
        }
        span {
            color: #fff;
            &.desktoptext {
                // float: right;
                // height: 22px;
                // margin: -4px 0 0 4px;
                vertical-align: middle;
            }
        }
        
        @media(max-width: @screen-md) {
            .desktoptext {
                display: none;
            }
        }
    }
    @media(max-width: @screen-md) {
        .primarysingleaction {
            .desktoptext {
                display: inline-block;
            }
            margin-right:24px;
        }
    }
    .sprite {
        display: inline-block;
        // vertical-align: middle;
        background-color: transparent;
        border: 0;
        vertical-align: middle;
        margin-bottom: 2px;
        &.q-content_Retrieve {
            @media(max-width: @screen-sm) {
                vertical-align: top;
            }
        }
        &.q-content_Submit {
            @media(max-width: @screen-sm) {
                vertical-align: top;
            }
        }
    }
    ul.dropdown-menu {
        top: 7px;
        >li {
            padding: 2px 0 !important;
        }
        &.dropdownHubmenu {
            left: 1rem;
            min-width: 200px;
        }
    }
}

.dropdown-menu li.divider:hover {
    background-color: @white  !important;
}

.dropdownPersonamenu,
.dropdownSchedulermenu,
.dropdownPersonamenu {
    .divider {
        height: 1px !important;
    }
    z-index: 2;
    position: absolute;
    height: 0;
    width: 0;
    overflow: hidden;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
    opacity: 0;
    background: @white;
    .box-shadow(@string);
    li {
        line-height: 26px !important;
        a {
            line-height: 26px !important;
        }
    }
}

.open>.dropdownUsermenu,
.open>.dropdownSchedulermenu,
.open>.dropdownPersonamenu,
.open>.dropdownAlertsmenu {
    display: block;
    height: auto;
    width: auto;
    min-width: 200px;
    opacity: 1;
}

.open>.q-header {
    z-index: 999;
    /*Required for iOS to receive touch on links */
    position: absolute;
    top: 7rem;
    right: 1.5rem;
    display: block;
    height: auto;
    width: auto;
    min-height: 200px;
    min-width: 260px;
    max-width: 90%;
    overflow: hidden;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
    opacity: 1;
    background: @white;
    .box-shadow(@string);
}

