    }
}

.nosmallmobile {
    display: block;
    @media(max-width: @screen-sm) {
        display:none;
    }
}


.header-mobile {
    @media(min-width: @screen-lg) {
        display: none;
    }
}


.onlymobile {
    @media(min-width: @screen-lg) {
