// #68.. header text wrap
.q-header_User .q-header_Item,
.q-header_Scheduler .q-header_Item,
.q-header_Persona .q-header_Item {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    width: calc(100% - 28px);
    display: inline-block;
    margin: 0;
    text-align: right;
    color: @white;
    font-size: @font-standard;
}

.q-header_ItemRed {
    color: @white;
    span.sprite {
        margin-top: 11px;
        display: inline-block;
    }
}

