/* Flex layout */

.header-layout {
    display: flex;
    background: #047bc1;
    height: 40px;
}

.header-menu {
    padding: 0 5px;
    background: #2b3f62;
}


.header-link {
    h1, .breadcrumb .active {
        display: none;
    }
    .breadcrumb li {
        padding: 12px 2px 0 8px;
        @media(max-width: @screen-sm) {
            padding-top: 6px;
        }
        .opacity(0.54);
        width: 100%;
        &:hover {
            .opacity(0.87);
        }
    }
} 

.header-title {
    flex: 1;
    text-align: center;
    @media(max-width: @screen-sm-max) {
        text-align: left;
    }
    @media(max-width: @screen-xs-max) {
        text-align: left;
        max-width: 230px;
    }
    h1 {
        sup {
            color: #fff;
            top: 0;
        }
        .header-text {
            color: @white !important;
            font-size: 16px !important;
            font-weight: 500;
        }
    }
}
.header-title .breadcrumb {
    display: none;
}

