    color: #fff;
    line-height: 14px;
    font-size: 10px;
    top: 4px;
    right: 2px;
}

#q_autocomplete.float_search_auto {
    width: 400px;
    position: absolute;
    right: 0;
    top: 0;
    background: @dashblue;
    margin: 0;
    height: 40px;
    z-index: 8002;
    .q-form-control-wrapper {
        margin-top: -16px;
        input.q-form-control-input {
            background-image: linear-gradient(#ffffff, #ffffff), linear-gradient(#d2d2d2, #d2d2d2);
            color: @white;
        }
        input.q-form-control-input::-webkit-input-placeholder,
