    }
}

/* Search pane */

.q-overlay-search {
    top: 40px;
}

div#q_autocomplete {
    width: 400px;
    position: absolute;
    right: 0;
    top: 0;
    background: #047bc1;
    margin: 0;
    height: 40px;
    z-index: 8002;

    .k-autocomplete .k-icon.k-i-loading {
        background-image: url(images/loadingindicator_white.gif) !important;
    }
    
    span.k-icon.k-clear-value.k-i-close {
        filter: invert(100%)!important;
    }

    .q-form-control-wrapper {
        margin-top: -16px;

        input.q-form-control-input {
            background-image: linear-gradient(#ffffff, #ffffff), linear-gradient(#d2d2d2, #d2d2d2);
            color: #fff;
     
                &::-webkit-input-placeholder { /* Edge */
                    color: #fff;
                }

                &:-ms-input-placeholder { /* Internet Explorer 10-11 */
                    color: #fff;
                }

                &::placeholder {
                    color: #fff;
                }
            }
        }
}

