// Any style classes for the Filter Dataset pattern
body .material.q-form,
.container .material.q-form,
.container-fluid .material.q-form,
.q-form {
	height: auto;
	// border: 1px solid @grayborder;
	// border-radius: 4px;
	// .material-gradient(@dashblue, @faintgray, 6px);
	background: @faintgray;
}

.accordion, .add {
	position: absolute;
	top: 2rem;
	right: 2rem;
	opacity: 0.5;
	background-size: 356px;
	cursor: pointer;
	&:hover { opacity: 0.75; }
}

.add {
	top: 1.714em;
	right:6.125em;
	z-index:8;
}

