.accordiontext {
	width:120px;
	position: relative;
	display:block;
	cursor: pointer;
	margin-top:12px;

	&:before {

		content: "Show less";
		display:inline-block;
		width:80px;
		font-size:14px;
		color:@dashblue;
	}

	&:after {

		content: " ";
		.sprite;
		.q-content_arrow_up;
		width: 18px;
		height: 18px;
		.opacity(0.27);
		position:absolute;
		top:-2px;
		left:80px;
	}
}


.accordiontext.collapsed {

	margin-top:12px;

	&:before {

		content: "Show more";
	}
	&:after {

		content: " ";
		.sprite;
		.q-content_arrow_down;
		width: 18px;
		height: 18px;
		.opacity(0.27);
		position:absolute;
		top:-2px;
		left:80px;
	}
}


.accordionfilter .q-form-control-wrapper {
	min-height: 4em;
}
