// All Hub Widgets appear here


.widget {
  padding:0 0 0 5px!important;
}

// Reset divs
.widget > div, .widget * > div {
  padding:0;
  margin:0;
}

.widget {
  h1, h2, h3, h4, h5, h6, p, span {
  margin:0;
  padding:0;
  color:@black;
  }
}
a:hover {
text-decoration:none;
}

// Add Widget

.widget {
  .widget {
    background-image: none;
    margin-left: 1px;
  }
}

// Typography



.widget h1 {
  font-size:16px;
  padding: 8px 40px 8px 16px;
}

.widget h2 {
  font-size:15px;
  padding:8px 16px;
}

.widget {
    h3, h4, h5, h6 {
    font-size:14px;
    padding: 8px 16px 16px 16px;
    .opacity(0.87);
    }
  }

.widget {
    h1, h2, h3, h4, h5, h6 {
    .opacity(0.87);
    text-overflow:ellipsis;
    white-space:nowrap;
    overflow:hidden;
    display:block;
    }
  }

.q-widget-not-found {
  padding:8px 10px;
  display: inline-block;
}
// Main widgets

.alertlabel, 
.datalabel, 
.errorfreelabel, 
.q-widget-total-label {
    text-overflow:ellipsis;
    white-space:nowrap;
    overflow:hidden;
    display:block;
    color: @dashblue!important;
    font-size:14px;
}

@media (max-width: @screen-sm-max) {
  .datalabel, .alertlabel {
    margin-top:12px!important;
    }
  .datalabel:nth-child(1), .alertlabel:nth-child(1) {
    margin-top:0!important;
  }
  }

.alertlabel {
  margin: 0 6px!important;
  }

.alertdata,
.alertdatasmall {
  color:@black;
  .opacity(0.87);
  display:inline-block;
  overflow:visible;
    &:before {
       content: "";
       width: 26px;
       height: 26px;
       display:inline-block;
       .q-hub_Alert;
       .sprite;
        .opacity(0.54);
    }
  }

.alertdata, .q-widget-total-large {
  font-size:70px;
  line-height:54px;
  font-weight:400;
    &:before {
      margin: 0 4px 15px 0;
    }
}

.alertdatasmall,
.datamedium,
.q-widget-total-medium
{
	font-size:50px;
	line-height:38px;
	font-weight:400;
	&:before {
		margin: 0 4px 4px 0;
	}
}

.errorfreedata {
  color:@black;
  .opacity(0.27);
  display:inline-block;
  overflow:visible;
    // &:before {
    //    content: "";
    //    width: 26px;
    //    height: 26px;
    //    display:inline-block;

    //    .q-hub_AwaitingApproval;
    //    .sprite;
    //     .opacity(0.27);
    //  margin-top:15px!important;
    // }
  }

.datamedium:before,
.q-widget-total-medium:before {
  margin: 0 4px 4px 0!important;
}

.datalabel {
  display:block;
  text-transform:capitalize;
  }


.data, .datasmall, .datatext, .datatextlarge, .q-widget-total-small 
{
  color:@black;
  .opacity(0.87);
  }

.data, .q-widget-total-small 
{
  font-size:35px;
  line-height:28px;
  font-weight:400;
}

.datasmall {
  font-size:24px;
  line-height:26px;
  vertical-align: top;
  font-weight:400;
}

.datatext, .datatextlarge {
