  }
}


.cycleopen {
  .opacity(0.54);
}

.cycleclosed, .cyclepending {
  .opacity(0.27);
}

.carousel-inner {
  height:120px;
  .item:not(.section-item) {
    span:first-child {
      overflow:hidden;
      display:block;
      max-height:64px;
      width:100%;
    }
  }
}

.carousel-control {
  background-image:none!important;
  .opacity(1);
}

.right .q-content_expand {
  margin-top:8px;
  .opacity(0.27);
  -ms-transform: rotate(-90deg); // IE 9
    -webkit-transform: rotate(-90deg); // Chrome, Safari, Opera
    transform: rotate(-90deg);
  &:hover {
    .opacity(0.54);
  }
}

.left .q-content_expand {
  margin-top:8px;
  .opacity(0.27);
  -ms-transform: rotate(90deg); // IE 9
    -webkit-transform: rotate(90deg); // Chrome, Safari, Opera
    transform: rotate(90deg);
  &:hover {
    .opacity(0.54);
  }
}

.carousel-indicators li {
  .opacity(0.27);
  background-color:@black!important;
  width:12px;
  height:12px;
  margin:0;
}

.carousel-indicators .active {
  .opacity(0.54);
}

.carousel-inner {
  width:70%;
  margin:0 auto!important;
}

.item:not(.section-item) {
  .opacity(1);
}

.item:not(.section-item) span {
  font-size:14px;
  .opacity(0.87);
  display:block;
}

.item:not(.section-item) span:nth-child(2) {
  font-size: 12px;
  .opacity(0.54);
}

.item:not(.section-item) {
  height:80px;
}

@media (max-width: @screen-md-max) and (min-width: @screen-sm-min) {

  .item:not(.section-item) {
  height:120px;
  }
}

      .carousel-section {
         .carousel-control {
                  width: 10%!important;
                }

            .carousel-control:hover .carousel-section-control  {
                  .opacity(0.87);
                  }
       }

          .carousel-section-control {
            position: absolute;
            top: calc(50% - 18px);
            left: calc(50% - 9px);
            margin-top: 0!important;
          }

          .carousel-inner-section {
            width: 80%!important;
          }

          .carousel-inner-section, .carousel-inner-section .item {
            height: auto!important;
          }

          .carousel-inner-section .item {
            padding-bottom: 24px;
          }

          .section-item {
            .opacity(1);
          }

          .singledataarea {
            padding: 4px 0 2px 12px!important;
          }

          .singledataarea:first-child {
            padding: 12px 0 2px 12px!important;
          }


.carousel-indicators {
  bottom:0;
}

