// Readings, Measurements, Alerts

.dth:after, .mcf:after, .mmbtu:after, .gj:after, .gal:after, .usd:before {
  font-size:10px;
  color: @hinttext;
  margin: 0 0 0 2px;
  line-height:26px;
  vertical-align:top;
}

.dth:after{
  content: "Dth";
}

.gj:after{
  content: "GJ";
}

.mcf:after {
  content: "MCF";
}

.mmbtu:after {
  content:"MMBTU";
}

.gal:after {
  content: "Gal";
}

.usd:before {
  font-size:14px;
  color: @hinttext;
  margin: 0 4px 0 0;
  line-height:26px;
  vertical-align:top;
  content: "$";
}



.prelim:after {
  content: "(Do Not Pay)";
  font-size:18px;
  color: @brand-danger;
  margin: 0 0 0 2px;
  line-height:26px;
  vertical-align:top;
  .opacity(1);

}

