	}

	.k-datetimepicker .k-picker-wrap.k-state-disabled {
		padding-right: 0;
	}

	.k-dropdown-wrap, .k-picker-wrap, .k-numeric-wrap {
		padding: 0 2.571em 0 0 !important;
	}

	.datepicker-openended .btnopenended {
		right: auto;
	}

	.k-button{
		min-width: 100%;
	}
	
	.k-datetimepicker .k-picker-wrap {
		padding-right: 5em !important;
	}

	span.k-checkarea.q-form-checkarea-wrapper {
		padding-bottom: 20px;
	}

	span.k-checkarea.q-form-checkarea-wrapper .tooltip.left {
		margin-left: -20px;
	}

	.k-checkbox:disabled:checked + .k-checkbox-label:active:after{
		box-shadow: 0 0 0 12px rgba(235, 235, 235, 0.3);
	}
}

@media (max-width: 1199px){
.flex-widget-parent {
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	-webkit-flex-wrap: wrap;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
}
.flex-widget-child {
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
}

.flex-widget-child .widget {
	width: 100%;
}
}

