.group {
  background-color: @white;
  padding: 15px;
  border-radius: 5px;
  border: solid 1px #666;
}
// Allow tooltips to appear
.group,
.groupcontrols,
.groupcontrols2 {
  overflow: visible!important;
}

.group-conditions {
  margin: 8px 8px 8px 24px;
  border: 1px solid @grayborder;
}

.alert-group {
  margin-top: 10px;
  margin-bottom: 10px;
  border-color: rgb(192, 152, 83);
  border-color: rgb(251, 238, 213);
  border-color: rgb(220, 200, 150);
}

.condition {
  min-height: 60px;
  padding: 8px;
  position: relative;
}

.condition,
.group,
.querygroup {
  .andor {
    display: inline-block;
    width: 80px;
  }

  .querybuttons {
    background: transparent!important;
    border: 0!important;
    .opacity(0.54);

    &:hover {
      .opacity(0.87);
    }
  }
}

.querystring {
  margin-bottom: 16px;
}

.querygroup {
  margin: 16px 0 12px 8px;
  border: 1px solid @grayborder;
  padding: 8px;
  position: relative;

  .k-checkarea {
    margin-top: 8px!important;
  }

  .conditioncontrols,
  .groupcontrols,
  .groupcontrols2,
  .operatorcontrols {
    display: inline-block;
    padding-right: 12px;
    padding-left: 6px;
    height: 40px;
    overflow: hidden;
    vertical-align: middle;
    width: auto;
    min-width: 100px;

    .q-form-control-wrapper {
      min-height: inherit!important;
    }

    .infolabel {
      margin-top: 0;
    }
  }

  .groupcontrols2 {
    padding-top: 6px;
    float: right;
    text-align: right;
    position: absolute;
    top: 8px;
    right: 8px;
  }

  .conditioncontrols,
  .operatorcontrols {
    border: 1px solid @grayborder;
  }
}

.queryholder,
.queryholdersmall {
  display: inline-block;
  margin-right: 16px;
}

.queryholder {
  width: 110px;
}

.queryholdersmall {
  width: 60px;
}
