// Report launcher screen

.k-progressbar {
  background-color: lighten(@dashblue,25%)!important;
  border-color: transparent!important;
  height: 4px!important;

  .k-progress-status-wrap {
    .k-progress-status {
      display: none;
    }
  }

  .k-state-selected {
    background-color: @dashblue!important;
    border-color: transparent!important;
    height: 4px!important;
  }
}

.process-desc {
  width: 100%;
  font-size: 15px;
  color: @secondarytext;
  white-space: nowrap;
}

.processheader {
  padding-bottom: 24px;
  border-bottom: 1px solid rgba(0,0,0,0.07);
}

.k-widget.k-popup {
  background-color: transparent;
}

.k-list-container {
  // To offset above transparency
  background: @white;
}

.processdialog {
  @media(min-width:541px) {
    width: 420px;
  }
  @media(max-width:540px) {
    width: 300px;
  }
}

.processindicator {
  .opacity(0.54);
  text-align: center;
  margin-top: 16px!important;

  .error,
  .processing,
  .success,
  .warning {
    margin: 0 auto;
    cursor: default!important;
  }

  .error {
    .q-hub_Alert!important;
    margin: 0 auto;
  }

  .success {
    .q-hub_Complete!important;
    margin: 0 auto;
  }

  .warning {
    .q-hub_Warning!important;
    margin: 0 auto;
  }
 .processing {
  	.q-hub_InProgress!important;
   	margin: 0 auto 0 auto;
 }
}

.side-panel .q-form-control-wrapper .current-filter {
  background-color: @white!important;
}

.procspacing {
  padding-bottom: 12px!important;
}

.processTimedisplay,
.processdialog h1 {
  padding-left: 8px!important;
}

.processTimedisplay,
.procstatusInfo {
  .opacity(0.54);
}
// Report widget

.k-listview {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0!important;
    width: calc(100% - 130px);
  }

  .grid-view {
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      margin: 0!important;
      width: 90%!important;
    }
  }
}

.process .divider {
  border-bottom: none;
}

.process .actions {
  margin-top: -26px;
  padding-right: 16px!important;
}
// Pending changes
// .grid-view:nth-child(odd):not(:nth-child(1)) {
// 	     clear: both;
// 	 }

.grid-view {
  // p {
  // 	height: auto!important;
  // }
  .reportdetail {
    display: block!important;
  }

  .divider {
    border-bottom: 1px solid rgba(0, 0, 0, 0.27)!important;
  }

  .actions {
    padding: 0 16px 12px!important;
    margin-top: 0!important;
  }
}

.process {
  .reportdetail {
    display: none;

    p {
      margin: 16px;
      color: @secondarytext;
    }
  }

  .actions {
    a.removeFavorite {
      .opacity(0.84);
    }

    a {
      .opacity(0.54);

      &:hover {
        .opacity(0.84);
      }
    }
  }
}

// Report screen pagination
.current-processes + .k-pager-wrap {
  clear: both;
  background: transparent!important;
  .k-pager-numbers .k-state-selected, .k-pager-wrap {
    background: transparent!important;
    border: 0;
  }
}

