.footer-compact {
    height: 26px;
    background: @gray-footer;
    padding: 0 12px;
    display: flex;
}
.footer-left, .footer-center, .footer-right {
    flex: 1;
}

.footer-left a {
    display: block;
    margin-top: 2px;
}

/* Footer links */

button.footer-link {
    color: @white;
    background: transparent;
    border: 0;
    padding-top: 4px;
}

a.footer-link { 
    color: @white;
    &:hover, &:focus {
        text-decoration: none;
        color: @white;
    }
}

/* Open Screen control */

.openscreenscontrol {
    background: transparent;
    position: relative;
    bottom: 0;
    width: 180px;
    height: 24px;
    border: 0;
    border-radius: 0;
    margin-top: 1px;

    .openscreencount {
        color: @white;
    }

    span:not(.openscreencountholder) {
        margin-top: 0;
        margin-left: 0;
    }
    .openscreencountholder {
        display: block;
        height: 24px;
        line-height: 24px;
        font-size: 12px;
        color: #ffffff;
        position: absolute;
        padding: 0;
        margin: 0;
        top: 0;
        right: 20px;
        width: 120px;
        &:after {
            content: '';
            width:12px;
            height:12px;
        }
    }
}
.openscreendropdown {
    min-width: 300px !important;
    position: fixed !important;
    bottom: 32px !important;
    right: calc(50% - 150px) !important;
    top: inherit !important;
    left: inherit !important;
}
.openscreendropdown .modmark {
    margin-left: 4px;
}
.openscreendropdown li>a:not(.button) {
    display: inline-block;
    border: 0;
    width: calc(100% - 34px) !important;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.openscreendropdown>li>.button {
    display: none;
}
.openscreendropdown>li:hover>.button {
    display: inline-block;
    position: absolute;
    top: -4px;
    border: 0;
    background-color: transparent;
    width: 30px !important;
    padding: 2px 2px 2px 14px !important
}
.openscreendropdown>li:last-child a:not(.button) {
    width: 100% !important;
}
.openscreendropdown .button:hover {
    cursor: pointer;
}
.openscreendropdown .button:focus:before,
.openscreendropdown .button:active:before,
.openscreendropdown .button:focus {
    background: transparent !important;
}
.openscreendropdown .button:before,
.openscreendropdown .button:hover:before {
    background: transparent !important;
}

.k-edge header,
.k-ie header {
    .openscreenscontrol {
        .openscreencountholder {
            top: 16px !important;
        }
    }
}

/* Open screens control */

.bodylock {
    #openscreenscontrol {
       z-index:0; 
    }
}
