//
// Dropdowns
// --------------------------------------------------
.dropdown-menu>li>a:hover,
.dropdown-menu>li>a:focus {
    #gradient>.vertical(@start-color: @dropdown-link-hover-bg;
    @end-color: darken(@dropdown-link-hover-bg, 5%));
    background-color: darken(@dropdown-link-hover-bg, 5%);
}

.dropdown-menu>.active>a,
.dropdown-menu>.active>a:hover,
.dropdown-menu>.active>a:focus {
    #gradient>.vertical(@start-color: @dropdown-link-active-bg;
    @end-color: darken(@dropdown-link-active-bg, 5%));
    background-color: darken(@dropdown-link-active-bg, 5%);
}

//
// Navbar
// --------------------------------------------------
// Default navbar
.navbar-default {
    #gradient>.vertical(@start-color: lighten(@navbar-default-bg, 10%);
    @end-color: @navbar-default-bg);
    .reset-filter(); // Remove gradient in IE<10 to fix bug where dropdowns don't get triggered
    border-radius: @navbar-border-radius;
    @shadow: inset 0 1px 0 rgba(255, 255, 255, .15),
    0 1px 5px rgba(0, 0, 0, .075);
    .box-shadow(@shadow);
    .navbar-nav>.open>a,
    .navbar-nav>.active>a {
        #gradient>.vertical(@start-color: darken(@navbar-default-link-active-bg, 5%);
        @end-color: darken(@navbar-default-link-active-bg, 2%));
        .box-shadow(inset 0 3px 9px rgba(0, 0, 0, .075));
    }
}

.navbar-brand,
.navbar-nav>li>a {
    text-shadow: 0 1px 0 rgba(255, 255, 255, .25);
}

// Inverted navbar
.navbar-inverse {
    #gradient>.vertical(@start-color: lighten(@navbar-inverse-bg, 10%);
    @end-color: @navbar-inverse-bg);
    .reset-filter(); // Remove gradient in IE<10 to fix bug where dropdowns don't get triggered; see https://github.com/twbs/bootstrap/issues/10257
    .navbar-nav>.open>a,
    .navbar-nav>.active>a {
        #gradient>.vertical(@start-color: @navbar-inverse-link-active-bg;
        @end-color: lighten(@navbar-inverse-link-active-bg, 2.5%));
        .box-shadow(inset 0 3px 9px rgba(0, 0, 0, .25));
    }
    .navbar-brand,
    .navbar-nav>li>a {
        text-shadow: 0 -1px 0 rgba(0, 0, 0, .25);
    }
}

// Undo rounded corners in static and fixed navbars
.navbar-static-top,
.navbar-fixed-top,
.navbar-fixed-bottom {
    border-radius: 0;
}

// Fix active state of dropdown items in collapsed mode
@media (max-width: @grid-float-breakpoint-max) {
    .navbar .navbar-nav .open .dropdown-menu>.active>a {
        &,
        &:hover,
        &:focus {
            color: #fff;
            #gradient>.vertical(@start-color: @dropdown-link-active-bg;
            @end-color: darken(@dropdown-link-active-bg, 5%));
        }
    }
}

//
// Alerts
// --------------------------------------------------
// Common styles
.alert {
    text-shadow: 0 1px 0 rgba(255, 255, 255, .2);
    @shadow: inset 0 1px 0 rgba(255, 255, 255, .25),
    0 1px 2px rgba(0, 0, 0, .05);
    .box-shadow(@shadow);
}

// Mixin for generating new styles
.alert-styles(@color) {
    #gradient>.vertical(@start-color: @color;
    @end-color: darken(@color, 7.5%));
    border-color: darken(@color, 15%);
}

// Apply the mixin to the alerts
.alert-success {
    .alert-styles(@alert-success-bg);
}

.alert-info {
    .alert-styles(@alert-info-bg);
}

.alert-warning {
    .alert-styles(@alert-warning-bg);
}

.alert-danger {
    .alert-styles(@alert-danger-bg);
}

.dropdown-menu {
    border: 0;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
    .divider {
        background-color: transparent;
    }
    li {
        overflow: hidden;
        position: relative;
        a:hover {
            background-color: transparent;
            color: @dashblue;
        }
    }
}

// Prevent highlight on mobile
* {
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-tap-highlight-color: transparent;
    &:focus {
        outline: 0;
    }
}

// Reset
a {
    &:hover,
    &:focus {
        color: @dashblue;
    }
}
