.toolbar {
  width: 100%;
  margin-bottom: 12px;
  text-align: right;

  button {
    span {
      vertical-align: middle;
    }
  }

  a {
    .opacity(0.54);

    &:hover {
      .opacity(0.87);
    }
  }
}

.card {
  width: 45%;
  float: left;
  margin-bottom: 16px;
  border: 1px solid @grayborder;
  padding: 16px;

  .actionarea {
    width: 100px;
    position: absolute;
    right: -40px;
    margin-top: 8px;
    display: inline-block;

    a {
      .opacity(0.54);

      &:hover {
        .opacity(0.87);
      }

      span {
        margin-right: 12px;
      }
    }
  }

  h2 {
    width: calc(100% - 100px);
    margin-right: 0;
    margin-top: 0;
    padding-right: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    display: inline-block;
  }
}

.addressaccordion {
  float: right;
  display: block;
  margin: 8px 16px 0 0 !important;
  .opacity(0.54);

  &:hover {
    .opacity(0.87);
    cursor: pointer;
  }
}

.toolbar {
  margin-right: 16px;

  button {
    background: transparent;
    border: 0;
    .opacity(0.54);

    &:hover {
      .opacity(0.87);
    }

    &:disabled {
      .opacity(0.27);
      cursor: default !important;
    }
  }
}

.card,
.detailscard {
  min-height: 60px;
  position: relative;
  margin-bottom: 16px;

  .btn,
  .cardbtn {
    cursor: pointer;
  }

  &:hover {
    background: @gray-faded;

    .actionarea,
    .cardbtn {
      display: inline-block!important;
    }
  }
  
  @media(max-width:480px) {
    .dtlbtn {
      margin-right: 12px;
    }
  }

  .status-indicator {
    position: absolute;
    top: 6px;
    left: 6px;
    display: block;
    .opacity(0.54);
    font-size: 12px;
    line-height: 8px;
  }

  .dtlbtn {
    padding-right: 0!important;
    display: inline-block;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    -moz-osx-font-smoothing: grayscale;
    position: relative;
    overflow: hidden;
    -webkit-transition-property: color;
    transition-property: color;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;

    &:before {
      content: "";
      position: absolute;
      z-index: -1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: #F3F3F3;
      border-radius: 100%;
      -webkit-transform: scale(0);
      transform: scale(0);
      -webkit-transition-property: transform;
      transition-property: transform;
      -webkit-transition-duration: 0.3s;
      transition-duration: 0.3s;
      -webkit-transition-timing-function: ease-out;
      transition-timing-function: ease-out;
    }

    &:active:before,
    &:hover:before {
      -webkit-transform: scale(2);
      transform: scale(2);
    }

    &:focus:before {
      background: @faintgray;
    }
    }


  .actionarea,
  .cardbtn {
    .opacity(0.54);
    margin-top: 12px;
    margin-bottom: 12px;
    float: right;
    display: none!important;

    &:hover {
      .opacity(0.87);
    }
    margin-left: 24px;
    @media(max-width: @screen-xs-max) {
      margin-left: 8px;
      display: inline-block!important;
    }
  }
  border: 1px solid @grayborder;
  padding: 8px;
}

#statusmessage,
#statusmessage2,
#statusmessage3,
#statusmessage4,
#statusmessage5,
#statusmessage6,
.statusmessage {
  display: none;
  float: left;

  a {
    .opacity(1)!important;
  }
  @media(max-width:640px) {
    float: none!important;
    margin: 12px auto 18px;
    text-align: center;
  }
}

.card_added,
.card_deleted,
.card_modified {
  .status-indicator {
    width: 10px;
    height: 10px;
    text-indent: 14px;
  }
}

.card_added {
  .status-indicator {
    .q-small_Added!important;
    .opacity(0.54);
  }
}

.card_edit,
.card_modified {
  .status-indicator {
    .q-small_AmendContract!important;
    .opacity(0.54);
  }
}

.card_deleted {
  .opacity(0.54);

  .status-indicator {
    .q-small_Deleted!important;
  }

  .accordion,
  .actionarea,
  .cardbtn {
    display: none!important;
  }

  &:hover {
    background: transparent!important;

    .accordion,
    .actionarea,
    .cardbtn {
      display: none!important;
    }
  }
}

.card_error {
  border: 1px solid @brand-danger;

  h2 {
    color: @brand-danger;

    &:after {
      content: "";
      position: absolute;
      display: inline-block;
      .q-contentc_Error;
      .sprite;
    }
  }
}

.addresscard,
.contactcard {
  .actionarea {
    width: auto;
    z-index: 10;
    display: block;
    text-align: right;
    right: 24px;
    margin-left: 0!important;

    a {
      margin-right: 12px;
    }

    a:last-of-type {
      margin-right: 0;
    }
  }

  h2 {
    margin-top: 12px;
    margin-left: 12px;
    line-height: 18px;
    width: 100%;
    white-space: normal;
  }

  h3 {
    margin-top: 16px;
    margin-bottom: 8px;
  }
}

.addresscard,
.contactcard,
.detailscard {
  .btn-link.pull-right {
    border-color: transparent;
    background-color: transparent;
    margin-top: 6px;
    padding: 10px;
    font-size: 14px;
    text-transform: uppercase;
    padding-right: 10px !important;
  }
}

.addresscard {
  width: 100%;
}

.contactcard {
  width: 47%;
  margin-left: 3%;
  @media(max-width:640px) {
    width: 100%;
    margin: 10px;
  }

  .accordion {
    top: 20px!important;
  }
}

.addressholder {
  padding: 16px;
  margin-bottom: 12px;

  .accordion {
    top: 2px;
  }

  &:hover {
    background: @gray-faded;
  }

  h2 {
    margin-top: 12px;
    margin-left: 12px;
    line-height: 18px;
    width: 100%;
    white-space: normal;
  }

  h2.title {
    width: calc(100% - 60px);
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    margin-top: 0;
  }

  p {
    padding-bottom: 16px;
  }
  border: 1px solid @grayborder;

  .card {
    background: @white;
  }
}

.cardholder .k-widget * {
  box-sizing: border-box!important;
}

.k-loading-image {
  background-size: 24px 24px;
}
