// Hot key Nav

.navcontent {
	padding:6px 12px 24px 12px;
	background:@white!important;
	border-color:@white!important;
	.box-shadow(0 4px 6px 0 rgba(0, 0, 0, 0.26));


	.breadcrumb {
		margin-top:0;
		margin-bottom:0;
		padding-top:0!important;
	}

	p:first-of-type {
		margin-top:2px;
	}


	p {
		margin-top:20px;
	}

	.helplink {
		display:block;
		padding:4px;

		&:after {
			content: "»";
			padding-left:10px;
		}
	}

	.backlink {
		display:block;
		padding:4px;
		margin-top:8px;

		&:before {
			content: "«";
			padding-right:10px;
		}
	}

	h2 {
		margin-top:2px;
		margin-bottom:2px!important;
	}

	h3, h4 {
		margin-bottom:8px!important;
	}

	.hotkeydescription {

	width:100%;
	text-align:left;
	display:block;

	.q-content_arrow_up, .q-content_arrow_down, .q-content_arrow_left, .q-content_arrow_right {
		.opacity(0.54);

		&:hover {
			.opacity(0.54);
		}
	}

	.keyboardbtn {
		display:inline-block;
		padding:12px;
		text-transform:uppercase;
		-webkit-border-radius: 4px;
		-moz-border-radius: 4px;
		border-radius: 4px;
		border:2px solid @secondarytext;
		color:@secondarytext;
		font-weight: bold;
    	margin: 0 4px;

	}

	.hotkeytrigger {
		float: right;
		margin-right:16px;
		margin-top:6px;
	}

	}

}


.keyboardbtn {
		display:inline-block;
		padding:5px;
		text-transform:uppercase;
		-webkit-border-radius: 4px;
		-moz-border-radius: 4px;
		border-radius: 4px;
		border:2px solid @secondarytext;
		color:@secondarytext;
		font-weight: bold;
    	margin: 0 4px;
		font-size:11px;

	}


// *************** New Menu *************************

.mm-menu {
	background: @white !important;
}

.k-mobile .mm-menu {
	width: 100vw;
}


#mainMenuSlideOut {

	 .mm-navbar .mm-close.mm-btn {
			top:0!important;
			.opacity(0.12);
			right: 6px;

			&:after {
				content:""!important;
				.sprite;
				.q-content_Cancel;
				width: 18px;
				height: 18px;
			}

			&:hover {
					.opacity(0.84);
				}

		}


.mm-next:after, .mm-arrow:after {
		border-color: rgba(0, 0, 0, .54);
	}



.poweredby {
	color: @hinttext;
	font-size:10px;
	text-transform:uppercase;
}


.q-logo {

   display:block;
   height:60px;
   width:100%;
   text-align:center;

   .q-logo-img, .q-logo-img-client {
	  display: inline-block;
	  height: 90px;
	  width: 300px;
	  background-attachment: fixed;
	  opacity: 1;
   }

	.q-logo-img {

	  background: @white url("images/logo_myquorum.png") no-repeat 1px 0;
      background-size: 300px;
	}

	.q-logo-img-client {
	  background: @white url("../40_Product/images/logo_client.png") no-repeat 1px 0;
	  background-size: 300px;
	}



  }

.q-app {

	display: inline-block !important;
    position: absolute!important;
    top: 34px !important;
    right: 45px;
	.opacity(1)!important;
}


.mainlink {
	font-size: 18px;
    padding: 8px 16px;
	color:@primarytext;
	.opacity(0.27);
	cursor:pointer;
	-webkit-transition: all ease 0.7s;
	-moz-transition: all ease 0.7s;
	transition: all ease 0.7s;
	&:hover {
		.opacity(0.54);
	}
}


.menueditlink {
	position:absolute;
	top:0;
	left:0;
	width:90px;
	height:60px;
	padding-top:16px;
	padding-left:36px;

	& > a > div {
		display:block;
		-webkit-transition: all ease 0.7s;
		-moz-transition: all ease 0.7s;
		transition: all ease 0.7s;
		.opacity(0);
	}

	&:hover > a > div {
		display:block;
		.opacity(0.54);

		&:hover {
			.opacity(0.87);
		}
	}
}

.mm-opened > a {
			font-weight:bold;
		}



ul.mm-listview li ul li > a {
	font-size:13px;
}

ul.mm-listview li ul li ul li a {
	font-size:12px;
}


// Hide the first option which should always be sitemap

ul.mm-listview li.hide-sitemap {
	display:none;
}


.linkactivated {
	.opacity(0.87)!important;
}


.mm-listview > li {
	border-left-style: solid;
    border-left-width: 15px;
}

.mm-vertical > li {
	border-left-style: none;
    border-left-width: 0!important;
}




// Hover states for sub categories

.mm-panels .mm-panel ul li ul li a, .mm-panels .mm-panel ul li ul li a + a {

	-webkit-transition: all ease 0.7s;
	-moz-transition: all ease 0.7s;
	transition: all ease 0.7s;

}

.mm-panels .mm-panel ul li ul li a:hover, .mm-panels .mm-panel ul li ul li a:hover + a {
    font-weight: 500;
	padding-left:24px;

	&.mm-arrow:after, &.mm-next:after {
		border-color:rgba(0, 0, 0, 0.87);
	}

}





