// *********** Main menu colors **********************************


@menuname: blank;
@menucolor: @dashblue;
@menucolor2: @dashblueDark;
@menuicon: none;

	// New Menu class below
#mainMenuSlideOut {
  [class^="q-menu"] {
    border-left-color: @menucolor!important;
  }

  [class^="q-menu"]:nth-child(2n) {
    border-left-color: @menucolor2!important;
  }
  // For single options only
  [class^="q-menu"]:not(.mm-vertical) {
    box-shadow: inset 0 0 0 0 @menucolor!important;
    // -webkit-transition: all ease 0.7s;
    // -moz-transition: all ease 0.7s;
    // transition: all ease 0.7s;
    &:hover {
      box-shadow: inset 426px 0 0 0 @menucolor!important;
      color: @white;
      background-color: rgba(0, 0, 0, 0.1)!important;
    }

    & > a.mm-next:hover {
      &:after {
        border-color: rgba(255, 255, 255, 1)!important;
      }
    }
  }

  [class^="q-menu"]:not(.mm-vertical):nth-child(2n) {
    box-shadow: inset 0 0 0 0 @menucolor2!important;
    // -webkit-transition: all ease 0.7s;
    // -moz-transition: all ease 0.7s;
    // transition: all ease 0.7s;
    &:hover {
      box-shadow: inset 426px 0 0 0 @menucolor2!important;
      color: @white;
      background-color: rgba(0, 0, 0, 0.1)!important;
    }

    & > a.mm-next:hover {
      &:after {
        border-color: rgba(255, 255, 255, 1)!important;
      }
    }
  }
  // For menu option with multiple children
  [class^="q-menu"].mm-vertical {
    // -webkit-transition: all ease 0.7s;
    // -moz-transition: all ease 0.7s;
    // transition: all ease 0.7s;

    &:hover > a:nth-child(2) {
      box-shadow: inset 426px 0 0 0 @menucolor!important;
    }

    &:hover > a {
      color: @white;
    }

    &.mm-opened > a {
      color: @black;
    }

    &.mm-opened,
    &:hover {
      & > .mm-arrow:after,
      & > .mm-next:after {
        border-color: rgba(255, 255, 255, .99)!important;
      }
    }

    &:hover {
      background-color: rgba(0, 0, 0, 0.1)!important;
    }

    &.active,
    &.mm-opened,
    &.open {
      background-color: transparent!important;
    }

    &.active > a:nth-child(2),
    &.open > a:nth-child(2),
    &:hover > a:nth-child(2) {
      color: @white;
      background-color: transparent!important;
    }

    &.mm-opened > a:nth-child(2) {
      box-shadow: inset 426px 0 0 0 lighten(@menucolor,15%)!important;
    }

    &.mm-opened > a:nth-child(2) {
      color: @white;
      background-color: transparent!important;
    }
  }

  [class^="q-menu"].mm-vertical:nth-child(2n) {
    // -webkit-transition: all ease 0.7s;
    // -moz-transition: all ease 0.7s;
    // transition: all ease 0.7s;
    & > a:nth-child(2) {
      box-shadow: inset 0 0 0 0 @menucolor2!important;
      // -webkit-transition: all ease 0.7s;
      // -moz-transition: all ease 0.7s;
      // transition: all ease 0.7s;
    }

    &:hover > a:nth-child(2) {
      box-shadow: inset 426px 0 0 0 @menucolor2!important;
    }

    &:hover > a {
      color: @white;
    }

    &.mm-opened > a {
      color: @black;
    }

    &.mm-opened,
    &:hover {
      & > .mm-arrow:after,
      & > .mm-next:after {
        border-color: rgba(255, 255, 255, .99)!important;
      }
    }

    &:hover {
      background-color: rgba(0, 0, 0, 0.1)!important;
    }

    &.active,
    &.mm-opened,
    &.open {
      background-color: transparent!important;
    }

    &.active > a:nth-child(2),
    &.open > a:nth-child(2),
    &:hover > a:nth-child(2) {
      color: @white;
      background-color: transparent!important;
    }

    &.mm-opened > a:nth-child(2) {
      box-shadow: inset 426px 0 0 0 lighten(@menucolor2,15%)!important;
    }

    &.mm-opened > a:nth-child(2) {
      color: @white;
      background-color: transparent!important;
    }
  }

  .mm-vertical > [class^="q-menu"]:nth-child(n):hover {
	box-shadow: none !important;
	background: transparent !important;
}

}



