.k-ie,
.k-edge {
  .multi-step-material .k-checkbox-label {

    &:after,
    &:before {
      margin-top: -14px !important;
    }
  }
}

.clear-status {
  right: 0;
  z-index: 2;
  cursor: pointer;
  margin: 12px 18px;
  position: absolute;
  display: inline-block;
  opacity: 0.54;

  &:hover {
    opacity: 0.87;
  }
}

.instanceenvironment {
  padding: 0 18px;
}

span.running {
  opacity: 1;
  color: @dashblue;
}

.packagestatus {
  margin: 0;
  text-align: left;
  padding: 0 0 0 18px;
  list-style-type: none;
  font-size: 12px;

  &.installed {
    margin-top: 18px;
  }

  .installstatus,
  .instancesstatus {
    span.success {
      color: @brand-success;
    }

    span.warnings {
      color: @brand-warning;
    }

    span.errors {
      color: @brand-danger;
    }

    span.gray {
      color: @gray;
    }
  }
}

.multi-step-material {
  margin: 0;
  padding: 0;

  >.row {
    margin: 0;
    height: 100%;
  }

  .col-xs-24.col-sm-8 {
    padding: 0;
    height: 100%;
  }

  .panewindow {
    height: 100%;
  }

  .panewindow:not(.firstpane) {
    border-left: 1px solid @grayOnGrid;
    background: rgba(232, 232, 232, 1);
    background: -webkit-gradient(linear, left top, right top, from(rgba(232, 232, 232, 1)), color-stop(2%, rgba(255, 255, 255, 1)));
    background: linear-gradient(to right, rgba(232, 232, 232, 1) 0%, rgba(255, 255, 255, 1) 2%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#e8e8e8', endColorstr='@white', GradientType=1);
  }

  .panewindow.firstpane {
    background-color: #f6f6f6;

    .firstpaneheader,
    .packageactionarea {
      background-color: @white;
    }

    .packageactionarea {
      display: flex;
      align-items: center;
      padding: 0 15px 15px;
      flex-flow: row nowrap;
      justify-content: center;
      overflow-x: auto;
      @media (max-width: @screen-md) {
      justify-content: left;
      padding-bottom: 0;
      }

      .packageiconholder {
        display: flex;
        max-width: 8rem;
        text-align: center;
        align-items: center;
        justify-content: center;
        flex-flow: column nowrap;
        margin-top: 1em;

          .packageaction {
            margin-top: 4px;
            font-size: 12px;
            line-height: 14px;
          }

        &:not(:last-child) {
          margin-right: 15px;
          padding-right: 15px;
          border-right: 1px solid @grayfooter;
        }
      }
    }
  }

  .firstpanecontent {
    overflow-y: auto;
    background-color: #f6f6f6;

    .packagelist {
      padding: 0;
      margin: 0;
      list-style: none;

      .k-checkarea {
        margin-bottom: 0;
      }
    }

    li.packagesummary {
      width: 100%;
      height: 30px;
      display: block;
      padding-left: 12px;

      h3 {
        display: inline-block;
        height: 30px;
        margin: 0;
        padding: 0;
        line-height: 26px;
        width: calc(100% - 16rem);
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }

      &:nth-child(odd) {
        background-color: @zebrastripe;
      }

      &:not(.activepackage):hover {
        cursor: pointer;
        background: @lightergray;
      }

      .k-checkarea {
        width: 22px;
        height: 30px;
        overflow: hidden;
        margin-top: 1px !important;
      }

      .k-checkbox-label {
        font-size: 15px;
        line-height: 30px;
        display: inline-block;

        &:after,
        &:before {
          margin-top: 6px;
        }
      }

      &.activepackage {
        background-color: @dashblue;
      }

      &.activepackage h3 {
        color: @white;
      }

      .emptylabel {
        display: block;
        width: 1px;
        height: 30px;
      }

      .summarystatus {
        float: right;
        height: 30px;
        width: 12rem;
        padding-top: 4px;

        >span {
          margin-right: 10px;
        }

        .sprite {
          width: 18px;
          height: 18px;
        }
      }

      .summarystatus:after {
        border-top: none !important;
        border-left: none !important;
        content: '';
        border: 2px solid transparent;
        display: block;
        position: relative;
        margin-top: -20px;
        margin-left: 75px;
        -webkit-transform: rotate(-45deg);
        transform: rotate(-45deg);
        border-color: rgba(0, 0, 0, 0.54);
        border-width: 4px !important;
        width: 12px !important;
        height: 12px !important;
      }

      &.activepackage .summarystatus:after {
        border-color: rgba(255, 255, 255, 0.5);
      }

      &.activepackage .summarystatus .updateicon {
        opacity: 0.5;
        .q-contentw_Update2
      }

      &.activepackage .summarystatus .updateicon.updateavailable {
        opacity: 1;
        .q-contentw_Update2
      }

      .summarystatus .statusicon {
        .opacity(0.27);
        .q-content_Status;

        &.statusinstalled,
        &.statuserror {
          .opacity(1);
        }

        &.statusinstalled {
          .q-contentc_StatusGreen;
        }

        &.statuserror {
          .q-contentc_StatusRed;
        }
      }

      .summarystatus .statusindicator {
        border: 0;
        width: 20px;
        height: 20px;
        position: relative;
        border-radius: 100%;
        display: inline-block;
        background-color: @gray;

        &::after {
          top: 4px;
          left: 4px;
          content: '';
          width: 12px;
          height: 12px;
          position: absolute;
          border-radius: 100%;
          display: inline-block;
          border: 1px solid @white;
        }

        +.tooltip .tooltip-inner {
          padding: 10px;
          max-width: none;
          white-space: nowrap;

          .packagestatus {
            padding: 0;
            font-size: 12px;
          }
        }

        &.uninstalled {
          background-color: @gray;
        }

        &.installed {
          background-color: @brand-success;
        }

        &.warning {
          background-color: @brand-warning;
        }

        &.error {
          background-color: @brand-danger;
        }

        &.instances-unequal::after {
          background: @gray;
        }

        &.instances-equal::after {
          background: @brand-success;
        }

        &.instances-differ-from-master::after {
          background: url('images/packagemanager_status.gif') center no-repeat;
        }

        &.instances-warnings::after {
          background: @brand-warning;
        }

        &.instances-errors::after {
          background: @brand-danger;
        }
      }

      .summarystatus .updateicon {
        opacity: 0.27;
        .q-content_Update2
      }

      .summarystatus .updateicon.updateavailable {
        opacity: 0.87;
        .q-content_Update2
      }
    }
  }

  .firstpanelog {
    margin: 0;
    background-color: white;
    border-top: 1px solid @grayfooter;
    text-align: center;
    padding: 4px 5px 5px 5px;
    font-size: 12px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

.firstpaneheader {
  height: 48px;
  padding: 8px 0 0 12px;

  h2 {
    margin-top: 0 !important;
    width: calc(100% - 120px);
    display: inline-block;
  }
}
.firstpanefilter {
  padding: 0 15px;
}
.firstpanefilter .searchfilter {
  margin-top: 4px;
}

.firstpaneactionblock {
  padding-top: 4px;
  width: 28px;
  display: inline-block;

  button {
    border: 0;
    background: transparent;
    .opacity(0.54);
    width: 28px;
    display: inline-block;

    &:hover {
      opacity: 0.87;
    }
  }
}

.secondpane {
  height: 100%;
  overflow-y: auto;

  .packagetitle {
    padding: 12px 12px 0 18px;
    font-size: 14px;
    color: @black;
    opacity: 0.87;
    white-space: nowrap;
    text-overflow: ellipsis;
    height: 33px;
    overflow: hidden;
    max-height: 33px;
    width: calc(100% - 72px);
    display: inline-block;
  }

  .packageversion {
    width: 64px;
    font-size: 12px;
    height: 33px;
    color: @black;
    white-space: nowrap;
    display: inline-block;
    text-align: right;
    padding: 12px 12px 0 0;
  }

  .haserrors {
    color: @brand-danger  !important;
    opacity: 1;
  }

  .packagedescription {
    padding: 4px 12px 0 18px;
    font-size: 12px;
    color: @black;
    opacity: 0.87;
    height: 55px;
    max-height: 55px;
  }

  .packagemanifestlink {
    padding: 0 12px 12px 18px;
    height: 28px;
    max-height: 28px;

    a {
      float: right;
      font-size: 12px;
    }
  }

  .packageactionarea {
    height: 52px;
    max-height: 52px;
  }

  .packagecontentlist {
    margin-top: 7px;
    overflow-y: auto;
    height: calc(100% - 219px);

    .packageoptionlist {
      padding: 0;
      margin: 0;
      list-style: none;
    }

    li.packageoption {
      width: 100%;
      height: 30px;
      display: block;
      padding-left: 18px;

      &.optiondisabled {
        &:hover {
          cursor: default !important;
        }

        opacity: 0.27;
      }

      h3 {
        display: inline-block;
        height: 30px;
        margin: 0;
        padding: 0;
        line-height: 26px;
        width: 98%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        position: relative;
      }

      &:nth-child(odd):not(.activeoption) {
        background: rgba(232, 232, 232, 1);
        background: -webkit-gradient(linear, left top, right top, from(rgba(232, 232, 232, 1)), color-stop(12%, @gridhighlight));
        background: linear-gradient(to right, rgba(232, 232, 232, 1) 0%, @gridhighlight 12%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#e8e8e8', endColorstr='@gridhighlight', GradientType=1);
      }

      &:not(.activeoption):hover {
        background: rgba(232, 232, 232, 1);
        background: -webkit-gradient(linear, left top, right top, from(rgba(232, 232, 232, 1)), color-stop(12%, @lightergray));
        background: linear-gradient(to right, rgba(232, 232, 232, 1) 0%, @lightergray 12%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#e8e8e8', endColorstr='@lightergray', GradientType=1);
      }

      &.activeoption {
        background-color: @dashblue  !important;

        h3 {
          color: @white  !important;
        }
      }

      &.needsconfiguration {
        h3 {
          color: @brand-danger;
          opacity: 1;
        }
      }

      &.activeoption.needsconfiguration {
        background-color: @brand-danger  !important;
      }

      h3:after {
        border-top: none !important;
        border-left: none !important;
        content: '';
        border: 2px solid transparent;
        display: block;
        position: absolute;
        margin-top: -17px;
        right: 12px;
        -webkit-transform: rotate(-45deg);
        transform: rotate(-45deg);
        border-color: rgba(0, 0, 0, 0.54);
        border-width: 4px !important;
        width: 12px !important;
        height: 12px !important;
      }

      &.activeoption h3:after {
        border-color: rgba(255, 255, 255, 0.5);
      }
    }
  }

  .packageactionarea {
    width: 100%;
    padding: 8px 12px;
    text-align: center;
  }

  .packageiconholder {
    display: inline-block;
    min-width: 40px;
    text-align: center;
    margin: 0 8px;

    .packageicon {
      width: 18px;
      opacity: 0.87;
      margin: 0 auto;
      display: block;
    }

    .packageaction {
      width: 100%;
      opacity: 0.87;
      display: block;
      font-size: 12px;
      text-transform: lowercase;
    }
  }

  .packageiconholder:hover {
    .packageicon {
      opacity: 1;
    }

    .packageaction {
      opacity: 1;
    }
  }
}

.thirdpane {

  >div {
    height: 100%;
    overflow-y: auto;
  }

  .haserrors {
    color: @brand-danger  !important;
    opacity: 1;
  }

  .thirdpaneheader {
    height: 48px;
    padding: 8px 0 0 12px;

    h2 {
      margin-top: 0 !important;
      width: calc(100% - 42px);
      display: inline-block;
    }
  }

  .thirdpaneactionblock {
    padding-top: 4px;
    width: 28px;
    display: inline-block;

    button {
      border: 0;
      background: transparent;
      .opacity(0.54);
      width: 28px;
      display: inline-block;

      &:hover {
        opacity: 0.87;
      }
    }
  }

  .packagelink {
    padding: 0 12px 12px 18px;
    height: 28px;
    max-height: 28px;

    a {
      float: right;
      font-size: 12px;
    }
  }

  .packagedetailsheader {
    width: 100%;
    opacity: 0.87;
    color: @black;
    font-size: 15px;
    padding: 12px 18px;
    white-space: nowrap;
    display: inline-block;
    text-overflow: ellipsis;
  }

  .packagedetailsintro {
    width: 100%;
    opacity: 0.87;
    color: @black;
    font-size: 12px;
    padding: 0 18px 12px 18px;
  }

  .packageactionarea {
    width: 100%;
    height: 52px;
    max-height: 52px;
    padding: 8px 12px;
    text-align: center;

    .packageiconholder {
      display: inline-block;
      min-width: 40px;
      text-align: center;
      margin: 0 8px;
    }

    .packageicon {
      display: block;
      width: 18px;
      margin: 0 auto;
      opacity: 0.87;
    }

    .packageaction {
      width: 100%;
      opacity: 0.87;
      color: @black;
      display: block;
      font-size: 12px;
      text-transform: lowercase;
    }
  }

  .packageEnvTitle {
    font-size: 15px;
    font-weight: bold;
    padding-left: 32px;
    padding-bottom: 12px;
  }

  .packageEnvTitle+.packagedetailslist {
    margin-top: 0;
  }

  .packageEnvTitle+.packagedetailslist li:last-child {
    border-bottom: 1px solid @grayOnGrid;
  }

  .packagedetailslist+.packageEnvTitle {
    margin-top: 24px;
  }

  .packagedetailslistitem:first-child:before {
    content: '';
    background: @grayOnGrid;
    width: 100%;
    height: 1px;
    display: block;
    margin-bottom: 12px;
  }

  .packagedetailslistitem:first-child {
    padding-top: 0 !important;
    border: 0 !important;
  }

  .packagedetailslistitem:last-child:after {
    content: '';
    background: @grayOnGrid;
    width: 100%;
    height: 1px;
    display: block;
    margin-top: 12px;
  }

  .packagedetailslistitem:last-child {
    padding-bottom: 0 !important;
    border: 0 !important;
  }

  .packagedetailslist {
    margin: 0;
    padding: 0;
    list-style: none;

    li.packagedetailslistitem {
      padding: 12px 12px 12px 32px;
      width: 100%;
      list-style: none;

      &:nth-child(odd) {
        background: rgba(232, 232, 232, 1);
        background: -webkit-gradient(linear, left top, right top, from(rgba(232, 232, 232, 1)), color-stop(12%, @gridhighlight));
        background: linear-gradient(to right, rgba(232, 232, 232, 1) 0%, @gridhighlight 12%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#e8e8e8', endColorstr='@gridhighlight', GradientType=1);
      }

      .contentHolder {
        width: 75%;
      }

      .contentHolderFull {

        width: 100%;
      }

      .contentHolder,
      .contentHolderFull {
        display: inline-block;

        .packageTitle {
          padding: 0 6px 0 0;
          font-size: 14px;
          color: @black;
          opacity: 0.87;
          width: 100%;
          display: block;
          word-break: break-all;
        }

        .packageBriefTruncated {
          max-height: 124px;
          word-break: break-all;
          overflow: hidden;
        }

        .packageBrief,
        .packageBriefTruncated {
          padding: 4px 6px 0 0;
          font-size: 12px;
          color: @black;
          opacity: 0.87;
          width: 100%;
          display: block;
        }

        .packageversion {
          font-size: 12px;
          padding: 0 0 4px 0;
          color: @black;
          white-space: nowrap;
          display: block;
          text-align: left;
        }

        .packagemanifestlink {
          padding: 6px 0 0 0;
          height: 28px;
          max-height: 28px;
          float: right;
        }

        .packagemanifestlink a {
          font-size: 12px;
        }

        .packageModTitle {
          width: 15%;
          float: left;
        }

        .packageModHost {
          width: calc(85% - 80px);
          display: inline-block;
          word-break: break-all;
        }

        .packagemoduleversion {
          width: 64px;
          float: right;
        }

        .installedWithErrors {
          color: @brand-danger;
        }

        .installed {
          color: @brand-success;
        }
      }

      .actionOrVersion {
        width: 22%;
        text-align: center;
        float: right;

        .needsconfiguration span {
          color: @brand-danger;
        }

        p {
          font-size: 13px;
        }

        .packageiconholder {
          display: inline-block;
          min-width: 40px;
          text-align: center;
          margin: 0 8px;
        }

        .packageicon {
          display: block;
          width: 18px;
          margin: 0 auto;
          opacity: 0.87;
        }

        .packageaction {
          display: block;
          width: 100%;
          text-transform: lowercase;
          font-size: 12px;
          color: @black;
          opacity: 0.87;
        }
      }
    }
  }
}

.secondpane,
.thirdpane {
  .calltoactionmessage {
    opacity: 0.27;
    font-size: 12px;
    color: @black;
    width: 100%;
    text-align: center;
  }
}


/* Versioning */

.multi-step-material {
  min-height: calc(100vh - 12rem);
  height: calc(100vh - 12rem);
}

.firstpanecontent {
  height: calc(100vh - 208px);
}

.filterActive .firstpanecontent {
  height: calc(100vh - 273px);
}

.versiontwo {

  .firstpanecontent {
    height: calc(100% - 154px);
  }

  .filterActive .firstpanecontent {
    height: calc(100% - 219px);
  }
}