.k-minus {
  .q-small_Minus!important;
}

.k-treeview-lines {
  .k-state-hover {
    background-color: @gray-faded;
  }
}

.k-treeview .k-state-focused {
  background-color: transparent!important;
}
// Org Maintenance Tree View Component
.treeviewholder {
  .k-treeview .k-item {
    margin-left: 6px;
  }

  .k-checkarea {
    margin-top: 0;
    margin-bottom: 0;
  }

  .k-i-collapse,
  .k-i-expand,
  .k-minus,
  .k-plus {
    margin-left: -18px;
    width: 14px!important;
    height: 14px!important;
  }

  .k-in > .sprite {
    margin: 0 6px;
    opacity: 0.54;
  }

  .k-checkbox-label {
    margin-right: 12px;
    padding-left: 0;
  }

  .droptool {
    float: right;
  }

  li > div {
    height: 30px;

    .k-state-focused,
    .k-state-hover,
    .k-state-selected {
      background: transparent;
    }
  }

  li > div:hover {
    background: @lightergray;
  }

  li > div .droptool {
    opacity: 0;
  }

  li > div:hover .droptool {
    opacity: 0.54;
  }

  li > div:hover .droptool:hover {
    opacity: 0.87;
  }

  h3 {
    display: inline-block;
    margin: 0;
    padding: 0;
  }

  .k-in.k-state-focused,
  .k-in.k-state-selected,
  .k-state-focused,
  .k-state-selected {
    background: rgba(4, 123, 193, 0.12)!important;
  }

  div.k-treeview {
    overflow: hidden;
  }

  span.k-in {
    width: calc(100% - 38px);
  }

  .k-i-expand,
  .k-plus {
    .q-grid_expand()!important;
  }

  .k-i-collapse,
  .k-minus {
    .q-grid_collapse()!important;
  }
}
